import React from 'react';
import {NavLink} from 'react-router-dom';
import plane from '../assets/plane.png'
import clouds from '../assets/clouds.png'
import '../styles/Home.css';

// This is the home page of the site.
const Home = function(props) {
    return (
        <div>
            <div id="grid-container">

                <div id="home-grid-box-1">
                    <img id="plane" alt="" src={plane}></img>
                    <img id="clouds" alt="" src={clouds}></img>
                </div>

                <div id="home-grid-box-2">
                    <div id="welcoming-text">
                        <h1 id="welcome-to">Welcome to</h1>
                        <h1 id="flights-center">Flights Center!</h1>
                    </div>
                    <div id="site-desc-cont">
                        <p id="site-description">This is the ultimate place to explore and find flight tickets 
                                                        to the best locations in the world!</p>
                    </div>
                </div>

                <div id="home-grid-box-3">
                    <div id="offers-container">
                        <NavLink to="/flights">
                            <div className="offer-btn-cont" id="find-flight">
                                <label>Find a Flight</label>
                            </div>
                        </NavLink>
                        <NavLink to="/deals">
                            <div className="offer-btn-cont" id="deals">
                                <label>Our Deals</label>
                            </div>
                        </NavLink>
                    </div>
                </div>

            </div>
        </div>
    )
}

export default Home;

