import React, {Component} from 'react';

// This is a simple button that being used in many places in the application.
// It gets all its data from the props.
class Button extends Component {

    handleClick = (e) => {
        this.props.onClick(e);
    }

    render() {
        return (
            <div className="fc-button" id={this.props.id} onClick={this.handleClick}>
                <label id={this.props.id+"-text"} className="fc-button-text">{this.props.text}&nbsp;&nbsp;</label>
                <img src={this.props.icon} id={this.props.id+"-icon"} alt="" className="fc-button-icon"/>
            </div>
        )
    }
}

export default Button;