import React, {Component} from 'react';
import showIcon from '../assets/show-pass.png';
import hideIcon from '../assets/hide-pass.png';

// This is a field that can hide values with sensitive information (like passwords).
// The value of the field will be showed only when the user clicks a dedicated button.
class HiddenField extends Component {

    state = {
        fieldVisable: false
    }

    toggleShowField = () => {
        this.setState({fieldVisable: !this.state.fieldVisable})
    }

    render() {
        return (
            <div className="hidden-field" id={this.props.id}>
                <label className="hidden-field-label" id={this.props.id+"-label"}>{this.props.label}&nbsp;</label>
                {this.state.fieldVisable && 
                    <div style={{display: 'inline-block'}}>
                        <label className="hidden-field-text" id={this.props.id+"-text"}>{this.props.value}&nbsp;</label>
                        <img src={hideIcon} alt="" className="hidden-field-icon" onClick={this.toggleShowField}/>
                    </div>
                }
                {!this.state.fieldVisable && 
                    <div style={{display: 'inline-block'}}>
                        <label className="hidden-field-text">****&nbsp;</label>
                        <img src={showIcon} alt="" className="hidden-field-icon" onClick={this.toggleShowField}/>
                    </div>
                }
            </div>
        )
    }
}

export default HiddenField;