import React, {Component} from 'react';
import {Link, NavLink, Redirect} from 'react-router-dom';
import SweetAlert from 'sweetalert2-react';
import {UserContext} from '../UserContext';
import AuthService from '../services/AuthService';
import ViewService from '../services/ViewService';
import Button from './Button';
import signUpIcon from '../assets/signup-icon.png';
import loginIcon from '../assets/login-icon.png';
import personIcon from '../assets/person-icon.png';
import userIcon from '../assets/folder-icon.png';
import closeIcon from '../assets/close-icon.png';

// This component is located at the right side to the top bar, and handles actions regarding to users.
// When it detects an anonymous user it will render a sign-up and login button.
// When it detects a registered user (or the admin) it will render a greeting message and a dropdown menu.
class UserActions extends Component {

    state = {
        loggedOff: false,
        btnClicked: 'none',
        loginMenuOpened: false,
        userMenuOpened: false
    }

    // Handles the logout from the system.
    logout = () => {
        let handleUserChange = this.context.handleLogin;
        AuthService.logout(handleUserChange);
        this.setState({
            loggedOff: true,
            loginMenuOpened: false
        })
    }

    // Handles the login / sign-up buttons click.
    setBtnClickState = (btnState) => {
        if (btnState == "sign-up")
        {
            this.setState({btnClicked: 'sign-up'})   
        }
        if (btnState == "login")
        {
            this.setState({btnClicked: 'login'})   
        }
        if (btnState == "user-page")
        {
            this.setState({btnClicked: 'user-page'})   
        }

        setTimeout(()=>
            this.setState({
                btnClicked: 'none',
                loginMenuOpened: false,
                userMenuOpened: false
            }),
        100);
    }
    
    // Handles the opening and closing of the login dropdown menu.
    // When it opens and it detects that the navigation menu is open too, it will close it.
    toggleLoginMenu = () => {
        this.setState({
            loginMenuOpened: !this.state.loginMenuOpened
        },()=> {
            if (this.props.navMenuOpen && this.state.loginMenuOpened)
            {
                this.props.userMenuOpened();
            }
        })
    }

    // Handles the opening and closing of the user dropdown menu.
    // When it opens and it detects that the navigation menu is open too, it will close it.
    toggleUserMenu = () => {
        this.setState({
            userMenuOpened: !this.state.userMenuOpened
        },()=> {
            if (this.props.navMenuOpen && this.state.userMenuOpened)
            {
                this.props.userMenuOpened();
            }
        })
    }

    // Listens to the Navbar component state: when the navigation dropdown menu is opened,
    // it closes the login menu.
    componentWillReceiveProps() {
        setTimeout(()=>{
            if (this.props.navMenuOpen)
            {
                this.setState({
                    loginMenuOpened: false,
                    userMenuOpened: false
                })
            }
        },5)
    }

    render() {
        let userContext = this.context;
        if (userContext.userInfo.role == "Anonymous")
        {
            return (
                <div className="user-actions">

                    {!ViewService.isMobileView() && // Desktop view
                        <div id="user-cont-desktop">
                            <Button id="sign-up-btn" onClick={()=>this.setBtnClickState('sign-up')} text="Sign Up" icon={signUpIcon}/>   
                            <Button id="login-user-btn" onClick={()=>this.setBtnClickState('login')} text="Login" icon={loginIcon}/>
                        </div>
                    }

                    {ViewService.isMobileView() && // Mobile view
                        <div id="user-cont-mobile">
                            <Button id="mobile-login-menu-btn" onClick={this.toggleLoginMenu} text="Login" icon={personIcon}/>
                            {this.state.loginMenuOpened &&
                                <div id="mobile-login-menu" className="mobile-menu">
                                    <Button id="sign-up-btn-mobile" onClick={()=>this.setBtnClickState('sign-up')} text="Sign Up" icon={signUpIcon}/>   
                                    <Button id="login-user-btn-mobile" onClick={()=>this.setBtnClickState('login')} text="Login" icon={loginIcon}/>
                                    <Button id="close-menu-btn" onClick={this.toggleLoginMenu} text="Close" icon={closeIcon}/>
                                </div>
                            }
                        </div>
                    }

                    <SweetAlert
                        success
                        show={this.state.loggedOff == true}
                        title={`Goodbye!`}
                        text="You are now logged out."
                        onConfirm={() => this.setState({ loggedOff: false })}
                        />

                    {this.state.btnClicked == 'login' &&
                        <Redirect to='/login'/>
                    }
                    {this.state.btnClicked == 'sign-up' &&
                        <Redirect to='/register'/>
                    }
                    {this.state.loggedOff == true &&
                        <Redirect to='/'/>
                    }

                </div>      
            )
        }
        else
        {
            if (!ViewService.isMobileView()) // Desktop view
            {
                return (
                    <div className="user-actions logged-in-greet">
                        <div className="dropdown-head">
                            <div className="greeting-container">
                                <label className="user-greet">Hello, {userContext.userInfo.name}!</label>
                                <img src={personIcon} alt="" href="/" className="person-icon"/>
                            </div>
                            <div className="dropdown-content">

                                <Link className="user-page-btn" to="/user-page">
                                    My Area
                                    <img className="user-icon" alt="" src={userIcon}/>
                                </Link>

                                <button className="logout-btn btn" onClick={this.logout}>
                                    Logout
                                    <img className="logout-icon" alt="" src={loginIcon}/>
                                </button>

                            </div>
                        </div>
                    </div>
                )
            }
            else // Mobile view
            {
                return (
                    <div className="user-actions mobile-user-name">
                            <div className="greeting-container" onClick={this.toggleUserMenu}>
                                <label className="user-greet">{userContext.userInfo.name}</label>
                                <img src={personIcon} alt="" href="/" className="person-icon"/>
                            </div>
                            {this.state.userMenuOpened &&
                                <div id="mobile-user-menu" className="mobile-menu">

                                    <Button id="user-page-btn-mobile" onClick={()=>this.setBtnClickState('user-page')} text="My Area" icon={userIcon}/>

                                    <Button id="logout-btn-mobile" onClick={this.logout} text="Logout" icon={loginIcon}/>

                                    <Button id="close-menu-btn" onClick={this.toggleUserMenu} text="Close" icon={closeIcon}/>

                                </div>
                            }
                            {this.state.btnClicked == 'user-page' &&
                                <Redirect to='/user-page'/>
                            }       
                    </div>
                )
            }

        }
    }
}

UserActions.contextType = UserContext;

export default UserActions;