import React from 'react';
import { gitHubRepositoryURL, contactEmail } from '../Constants';
import aboutImg from '../assets/about.jpg';
import '../styles/About.css';

// This is the about page.
const About = function(props) {

    return (
        <div>
            <div id="about-container" className="page-content-container">

                <img src={aboutImg} alt="" id="about-image"/>

                <div id="about-notice">
                    <label id="notice-label">Please notice:</label>
                    <p id="disclaimer">
                        This is a demo website.
                        All the information, flights, deals, airline companies and customers here are fake and created 
                        for the website to be tested and presented.
                    </p>
                </div>
      
                <div id="about-welcome">
                    <p>
                        My name is Yuval Barak and I am the creator of this website. <br/>
                        It was built for me to study and practice web developing.
                    </p>
                    <p>
                        In this website you can sign up as a customer or as an airline company, buy tickets for the flights you choose, 
                        explore the current deals and look at the flights board that displays all the departuring and landing flights.
                    </p>
                </div>

                <label id="git-hub-reference">For more details, check out the <a href={gitHubRepositoryURL}>GitHub Repository</a> of this project.</label>
                <label id="contact-email">If you have any questions, please contact me at <a href={contactEmail}>ybem01@gmail.com</a>.</label>
            </div>
        </div>
    )
}

export default About;