import React, {Component} from 'react';
import axios from 'axios';
import SweetAlert from 'react-bootstrap-sweetalert';
import AuthService from '../../services/AuthService';
import ErrorService from '../../services/ErrorService';
import CountriesService from '../../services/CountriesService';
import PendingAirline from './PendingAirline';
import { getAirlinesFromQueueURL, createAirlineAccountURL, rejectAirlineInQueueURL, } from '../../Constants';
import loadingData from '../../assets/loading-data.gif';
import errorIcon from '../../assets/error-icon.png';

// This component is a part of the admin area.
// It displays all the admin's incoming messages - which are new airline companies that are waiting for aproval.
class AdminInbox extends Component {

    state = {
        dataState: 'loading',
        airlines: [],
        aproveMessage: ''
    }

    CancelToken = axios.CancelToken;
    source = this.CancelToken.source();

    // Gets all airline from the register queue.
    getAirlines = () => {
        
        axios({
            url: getAirlinesFromQueueURL, 
            headers: AuthService.getAuthHeader().headers, 
            cancelToken: this.source.token 
        }).then((response) => {
            this.setState({ 
                airlines: response.data,
                dataState: 'loaded'
            })
        }).catch((error) => {
            if (axios.isCancel(error))
            {
                console.log('the AJAX request was canceled.');
            }
            else
            {
                ErrorService.logErrorToConsole(error);
                this.setState({ 
                    dataState: 'error'
                })
            }
        })
    }

    // Handles the airline aproval operation.
    aproveAirline = (airline, handleError) => {
        axios({
            method: 'POST',
            url: createAirlineAccountURL,
            headers: AuthService.getAuthHeader().headers,
            data: airline,
            cancelToken: this.source.token
        }).then(() => {
            this.removeAirlineFromList(airline.Name);
            this.setState({ 
                aproveMessage: `'${airline.Name}' was aproved`
            })
        }).catch((error) => {
            ErrorService.logErrorToConsole(error);
            handleError();
        })
    }
    // Handles the airline removal (reject) operation.
    removeAirline = (airline, handleError) => {
        axios({
            method: 'DELETE',
            url: rejectAirlineInQueueURL,
            headers: AuthService.getAuthHeader().headers,
            data: airline,
            cancelToken: this.source.token
        }).then(() => {
            this.removeAirlineFromList(airline.Name);
        }).catch((error) => {
            ErrorService.logErrorToConsole(error);
            handleError();
        })
    }

    // Removes the airline from the list (after aproval or rejection).
    removeAirlineFromList = (airlineName) => {
        let newAirlinesList = [];
        this.state.airlines.forEach(airline => {
            if (airline.Name != airlineName)
                newAirlinesList.push(airline);
        });
        this.setState({airlines: newAirlinesList})
    }

    initializeData = async () => {
        this.getAirlines();
    }

    componentDidMount() {
        this.initializeData();
    }

    componentWillUnmount() {
        this.source.cancel();
    }

    render() {

        if (this.state.dataState == 'loading')
        {
            return (
                <img src={loadingData} alt="" className="loading-user-view"/>
            )
        }
        if (this.state.dataState == 'error')
        {
            return (
                <div className="error-container">
                    <img src={errorIcon} alt="error" className="error-icon"/>
                    <h4 className="error-message">An error occurred while receiving the data. </h4>
                </div>
            )
        }
        if (this.state.airlines.length == 0)
        {
            return (
                <label>No messages to show.</label>
            )
        }

        let colorKey = -1; // This key is being used to give different color to each airline icon.

        // Mapping all airlines to PendingAirline components.
        let airlinesList = this.state.airlines.map(airline => {

            colorKey++;

            return (
                <PendingAirline airline={airline} key={airline.Name} colorKey={colorKey} 
                    aproveFunc={this.aproveAirline} removeFunc={this.removeAirline}/>
            )
        })

        return (
            <div id="inbox-container">
                <p className="instructions">These new airline companies have completed the registration and waiting for your aprroval.</p>
                { this.state.airlines.length != 0 && 
                    <label id="total-pending-airlines">You have {this.state.airlines.length} pending airlines:</label>
                }
                <div id="pending-airlines-list" className="list-view">
                    {airlinesList}
                </div>
                <SweetAlert 
                    success
                    show={this.state.aproveMessage != ''}
                    showConfirm
                    allowEscape
                    title={this.state.aproveMessage}
                    onConfirm={()=>{this.setState({aproveMessage: ''})}}
                    >
                </SweetAlert>
            </div>
        )
    }
}

export default AdminInbox;