import React, {Component} from 'react';
import {BrowserRouter, Route, Switch, Redirect} from 'react-router-dom';
import UserContextProvider from './UserContext';
import Navbar from './components/Navbar';
import Home from './components/Home';
import Flights from './components/Flights';
import About from './components/About';
import Login from './components/Login';
import Register from './components/Register';
import CustomerRegister from './components/CustomerRegister';
import AirlineRegister from './components/AirlineRegister';
import UserPage from './components/UserPage';
import PurchaseTicket from './components/PurchaseTicket';
import FlightsBoard from './components/FlightsBoard';
import DealsPage from './components/DealsPage';
import bgImage from './assets/sky-desktop.jpg'
import './styles/App.css';
import './styles/Navbar.css';
import './styles/GeneralStyles.css';

class App extends Component {

  render() {
    return (
      <UserContextProvider>
        <BrowserRouter>
          <div className="App">
            <img className="bg-image" alt="" src={bgImage}></img>
            <header className="App">
              <Navbar/>
              <Switch>
                <Route exact path='/' component={Home} /> {/* The home page */}
                <Route path='/flights' component={Flights}/> {/* The flights search page */}
                <Route exact path='/deals' component={DealsPage} /> {/* The deals page */}
                <Route exact path='/flights-board' component={FlightsBoard} /> {/* The flights board page */}
                <Route exact path='/about' component={About} /> {/* The about page */}
                <Route exact path='/login' component={Login} /> {/* The login page */}
                <Route exact path='/register' component={Register} /> {/* This page refers the user to the customer/airline registration pages */}
                <Route exact path='/customer-register' component={CustomerRegister} /> {/* The customer registration page */}
                <Route exact path='/airline-register' component={AirlineRegister} /> {/* The airline registration page */}
                <Route path='/user-page' component={UserPage} /> {/* The registered user page */}
                <Route path='/purchase-ticket' component={PurchaseTicket} /> {/* The ticket purchase page */}
              </Switch>
            </header>
          </div>
        </BrowserRouter>
      </UserContextProvider>
    );
  }
}

export default App;