import React, {Component} from 'react';

// This component renders the countries select field for the flights search form.
class CountrySelect extends Component {

    handleChange = (e) => {
        this.props.onChange(e);
    }

    render() {
        return (
            <div className="country-select-container">
                <label htmlFor={this.props.id}>{this.props.labelText}</label>
                <select id={this.props.id} className="form-control" onChange={this.handleChange} value={this.props.value} required>
                    {this.props.countries.map(country => <option key={country.value} value={country.value}>{country.display}</option>) }
                </select>
            </div>
        )
    }
}

export default CountrySelect;