import React, {Component} from 'react';

// This component renders the radio buttons for the flights search form.
// These buttons switch between one way flights search and roundtrip flights search.
class TripSelect extends Component {

    handleChange = (e) => {
        this.props.onChange(e);
    }

    render() {
        
        return (
            <div id="trip-select-container">
                <div className="custom-control custom-radio custom-control-inline">
                    <input type="radio" className="custom-control-input" id="roundtrip" name="radio-btn" checked={this.props.value} // defaultChecked
                        onChange={this.handleChange}></input>
                    <label className="custom-control-label" htmlFor="roundtrip">Roundtrip</label>
                </div>
                <div className="custom-control custom-radio custom-control-inline">
                    <input type="radio" className="custom-control-input" id="oneway" name="radio-btn" checked={!this.props.value}
                        onChange={this.handleChange}></input>
                    <label className="custom-control-label" htmlFor="oneway">One way</label>
                </div>
            </div>
        )
    }
}

export default TripSelect;