import React, {Component} from 'react';
import {Route, Redirect} from 'react-router-dom';
import axios from 'axios';
import SweetAlert from 'react-bootstrap-sweetalert';
import ErrorService from '../services/ErrorService';
import CountriesService from '../services/CountriesService';
import CountrySelect from './Flights/CountrySelect';
import TripSelect from './Flights/TripSelect';
import DateSelect from './Flights/DateSelect';
import SearchResults from './Flights/SearchResults';
import { getOneWayFlightsURL, getRoundtripFlightsURL} from '../Constants';
import errorIcon from '../assets/error-icon.png';
import loadingData from '../assets/loading-data.gif';
import '../styles/Flights.css';

// This is the flights search page.
class Flights extends Component {
    
    state = {
        roundtrip: true,
        origin: "",
        destination: "",
        departure: "",
        return: "",
        countries: [],
        flights: [],
        errorState: false,
        formError: '',
        submitState: "notSubmitted",
        buyTicketState: 'none',
        selectedFlights: '',
    }
    
    CancelToken = axios.CancelToken;
    source = this.CancelToken.source();

    // Gets all the countries from the server, so they can be used in the dropdowns.
    getCountries = async () => {

        let countries = await CountriesService.getCountries();
        if (countries != null)
        {
            let countriesOptions = countries.map(country => {
                return { value: country.Id, display: country.Name  }
            })
            countriesOptions = [{value: '', display: '- Select -'}].concat(countriesOptions)

            this.setState({
                countries: countriesOptions,
            });
        }
        else
        {
            this.setState({ errorState: true})
        }
    }

    // Checks if all the fields are valid and crossponds to the rules.
    validationCheck = () => {

        if (this.state.origin != "" && this.state.destination != "")
        {
            if (this.state.origin == this.state.destination)
            {
                this.setState( {
                    formError: "Origin and destination countries cannot be the same." 
                })
                return false;
            }
        }

        let thisDate = new Date().setHours(0,0,0);

        if (this.state.departure != "")
        {
            if (new Date(this.state.departure) < thisDate)
            {
                this.setState( {
                    formError: "Cannot select a date from the past." 
                })
                return false;
            }
        }

        if (this.state.return != "" && this.state.roundtrip)
        {
            if (new Date(this.state.return) < thisDate)
            {
                this.setState( {
                    formError: "Cannot select a date from the past." 
                })
                return false;
            }
        }

        if (this.state.departure != "" && this.state.return != ""  && this.state.roundtrip)
        {
            if (new Date(this.state.return) < new Date(this.state.departure))
            {
                this.setState( {
                    formError: "Return date cannot be before departure date." 
                })
                return false;
            }
        }

        this.setState( {
            formError: '' 
        })
        return true;
    }

    // This function is called when the app detects query parameters in the URL.
    handleUrlQueries = () => {

        this.insertQueryParamsToForm();

        // Sets the validation function to be called after the component updates.
        this.setState({},() => {
            this.validationCheck();
        });
        
        // Sets the get-flights function to be called after the component updates.
        setTimeout(() => {
            if (this.state.errorState == false)
            {
                this.getFlights();
            }
        },50);
    }

    // Inserts all the query parameters from the URL to the state.
    insertQueryParamsToForm = () => {
        let queriesString = this.props.history.location.search;
        let queriesBlocks = queriesString.split('&');
        let queriesValues = queriesBlocks.map((block) => {
            return block.split('=')[1];
        });
        if (queriesValues.length == 4)
        {
            this.setState({ 
                roundtrip: true,
                departure: queriesValues[0],
                return: queriesValues[1],
                origin: queriesValues[2],
                destination: queriesValues[3]
            }) 
        }
        else if (queriesValues.length == 3)
        {
            this.setState({ 
                roundtrip: false,
                departure: queriesValues[0],
                origin: queriesValues[1],
                destination: queriesValues[2]
            }) 
        }
    }

    // Returns the full URL of the API flights request, OR only the query parameters.
    getRequestURL = (requestType) => {

        let queryParams;
        let url;

        if (this.state.roundtrip)
        {
            queryParams = `/query?departureDate=${this.state.departure}` +
                          `&returnDate=${this.state.return}` +
                          `&originCountryId=${this.state.origin}` +
                          `&destinationCountryId=${this.state.destination}`;
            url = getRoundtripFlightsURL;
        }
        else
        {
            queryParams = `/query?departureDate=${this.state.departure}` +
                          `&originCountryId=${this.state.origin}` +
                          `&destinationCountryId=${this.state.destination}`;
            url = getOneWayFlightsURL
        }
        if (requestType == "paramsOnly")
        {
            return queryParams;
        }
        else if (requestType == "fullURL")
        {
            return url + queryParams;
        }
    }

    // Gets the flights from the server.
    getFlights = () => {

        this.setState({ 
            submitState: 'loading'
        }) 
        axios.get(this.getRequestURL("fullURL"))
        .then((response) => {
            this.setState({ 
                flights: response.data, 
                submitState: 'submitted'
            })
        }).catch((error) => {
            ErrorService.logErrorToConsole(error);
            this.setState({ 
                submitState: 'error'
            })
        })
    }

    // This function is called when a field in the form has been changed.
    handleChange = (e) => {
        if (e.target.type == 'radio')
        {
            this.setState( {
                roundtrip: (e.target.id == 'roundtrip'),
            })
        }
        else
        {
            this.setState( {
                [e.target.id]: e.target.value 
            })
        }
        // Sets the validation check function to be called after the component updates.
        this.setState({},() => {
            this.validationCheck();
        });
    }

    // Handles the submit of the search form.
    handleSubmit = (e) => {
        e.preventDefault();
        this.getFlights();
    }

    // Handles the purchasing of the ticket when the user clicks a flight.
    buyTicket = (departureFlightId, returnFlightId) => {

        let selectedFlights;

        if (returnFlightId == null)
        {
            selectedFlights = {
                flightType: 'one-way',
                departureFlightId: departureFlightId
            }
        }
        else
        {
            selectedFlights = {
                flightType: 'roundtrip',
                departureFlightId: departureFlightId,
                returnFlightId: returnFlightId
            }
        }

        this.setState({
            buyTicketState: 'buyAlert',
            selectedFlights: selectedFlights
        })
    }

    // Builds query the parameters of the selected flights.
    getSelectedFlightsParams = () => {

        let params = '/purchase-ticket/params?';
        const selectedFlights = this.state.selectedFlights;
        params += 'flight-type=' + selectedFlights.flightType;
        params += '&departure-flight-id=' + selectedFlights.departureFlightId;

        if (selectedFlights.flightType == 'roundtrip')
        {
            params += '&return-flight-id=' + selectedFlights.returnFlightId;
        }
        return params;
    }

    // When the page is loaded it will check if it has query params at the URL.
    componentDidMount() {
        this.getCountries();
        if (this.props.history.location.search != '')
            this.handleUrlQueries();
    };

    componentWillUnmount() {
        this.source.cancel();
    }

    render() {

        // In case of an error.
        if (this.state.errorState == true)
        {
            return( 
                <div id="flights-page-container" className="page-content-container">
                    <div className="error-container" id="flights-page-error">
                        <img src={errorIcon} alt="error" className="error-icon"/>
                        <h4 className="error-message">An error occurred while receiving the data. </h4>
                    </div>
                </div>
            )
        }
        // Untill all the countries are loaded to the component, it will present loading image.
        if (this.state.countries.length == 0)
        {
            return( 
                <img src={loadingData} alt="loading..." id="loading-flights-page"/>
            )
        }
        else
        {
            return (
                <div id="flights-page-container" className="page-content-container">
                    <h3 id="flights-search-header">Where would you like to go?</h3>

                    <form id="flights-search-form" onSubmit={this.handleSubmit}>

                        <TripSelect onChange={this.handleChange} value={this.state.roundtrip}/>

                        <div className="second-row">

                            <CountrySelect id="origin" countries={this.state.countries} onChange={this.handleChange} 
                                value={this.state.origin} labelText="From: &nbsp;"/>

                            <CountrySelect id="destination" countries={this.state.countries} onChange={this.handleChange} 
                                value={this.state.destination} labelText="To: &nbsp;"/>

                            <DateSelect id="departure" onChange={this.handleChange} value={this.state.departure} labelText="Departure: &nbsp;" show={true}/>

                            <DateSelect id="return" onChange={this.handleChange} value={this.state.return} labelText="Return: &nbsp;" show={this.state.roundtrip}/>

                            <button id="search-btn" type="submit" className="btn btn-primary" disabled={this.state.formError != ''}>Search</button>                  
                        </div>

                        {this.state.formError != '' &&
                            <p id="search-form-invalid-message">{this.state.formError}</p>
                        }
                        

                    </form>
                    {/* When query params where detected at the URL, it will show the SearchResults component. */}
                    <Route
                        path='/flights/query'
                        render={(props) => <SearchResults {...props} 
                            flights={this.state.flights} 
                            roundtrip={this.state.roundtrip} 
                            submitState={this.state.submitState} 
                            buyTicket={this.buyTicket}
                        />}
                    />

                    {/* When the user submits, a redirect element will alter the URL. */}
                    {this.state.submitState != 'notSubmitted' &&
                        <Redirect to={'/flights' + this.getRequestURL("paramsOnly")}/>
                    }

                    {/* An alert before buying a ticket: */}
                    <SweetAlert 
                        show={this.state.buyTicketState == 'buyAlert'}
                        showCancel
                        showConfirm
                        allowEscape
                        title="Proceed to buy ticket to this flight?"
                        onConfirm={()=>this.setState({buyTicketState: 'buyConfirmed'})}
                        onCancel={()=>this.setState({buyTicketState: 'none'})}
                        >
                    
                    {/* When the user confirms to buy the ticket, this will redirect him to the purchase page. */}
                    </SweetAlert>
                    {this.state.buyTicketState == 'buyConfirmed' &&
                        <Redirect to={this.getSelectedFlightsParams()}/>
                    }
                    
                </div>
            )
        }
    }
}

export default Flights;