import React, {Component} from 'react';
import axios from 'axios';
import SweetAlert from 'react-bootstrap-sweetalert';
import AuthService from '../../services/AuthService';
import ErrorService from '../../services/ErrorService';
import CountriesService from '../../services/CountriesService';
import {UserContext} from '../../UserContext';
import Button from '../Button';
import { createFlightURL } from '../../Constants';
import saveIcon from '../../assets/save-icon.png';
import cancelIcon from '../../assets/cancel-icon.png';
import loadingIcon from '../../assets/loading-data.gif';
import errorIcon from '../../assets/error-icon.png'
import '../../styles/Airline.css';

// This component is a part of the airline company area.
// It allows the airline to create a new flight and add it to the database.
class CreateFlight extends Component {
    
    state = {
        flight: '',
        countries: 'loading',
        createState: 'none',
        errorMessage: ''
    }

    // Resets all the the properties (and the fields that's binded) of the flight state.
    resetFlightState = () => {
        const flight = {
            Id: 0,
            AirlineCompanyId: this.context.userInfo.user.Id,
            OriginCountryCode: '',
            DestinationCountryCode: '',
            DepartureTime: '',
            LandingTime: '',
            RemainingTickets: '',
            TicketPrice: ''
        }
        this.setState({flight: flight});
    }

    // Gets all the countries from the service, so they can be used in the dropdown.
    getCountries = async () => {
        
        let countries = await CountriesService.getCountries();
        if (countries != null)
        {
            this.setState({
                countries: countries,
            });
        }
        else
        {
            this.setState({ countries: 'error'})
        }
    }

    // When a field in the form is changed, this updates the state.
    handleChange = (e) => {
        let flightClone = Object.assign({}, this.state.flight);
        flightClone[e.target.name] = e.target.value;
        this.setState({
            flight: flightClone
        })
    }

    // Creating the flight with the given details.
    createFlight = () => {
        if (!this.validateFlightDetails())
        {
            return;
        }

        this.setState({ 
            createState: 'loading'
        })
        
        axios({
            method: 'POST',
            url: createFlightURL, 
            headers: AuthService.getAuthHeader().headers,
            data: this.state.flight
        }).then(() => {          
            this.setState({createState: 'created'});
            this.resetFlightState();
        }).catch((error) => {
            ErrorService.logErrorToConsole(error);
            this.setState({ 
                createState: 'error',
            })
        })
    }

    // Validates that all the flight's data is filled and valid.
    validateFlightDetails = () => {
    
        const flight = this.state.flight;
        
        if (flight.OriginCountryCode == flight.DestinationCountryCode)
        {
            this.setState({ errorMessage: 'Origin country and destination country cannot be the same'})
            return false;
        }

        if (flight.OriginCountryCode == '' || flight.DestinationCountryCode == '' || 
            flight.RemainingTickets == '' || flight.TicketPrice == '' ||
            flight.DepartureTime == '' || flight.LandingTime == '')
        {
            this.setState({ errorMessage: 'All fields must be filled'})
            return false;
        }

        const departureTime = new Date(flight.DepartureTime);
        
        if (departureTime < new Date())
        {
            this.setState({ errorMessage: 'Departure time cannot be in the past.'})
            return false;
        }

        const landingTime = new Date(flight.LandingTime);

        if (landingTime < new Date())
        {
            this.setState({ errorMessage: 'Landing time cannot be in the past.'})
            return false;
        }

        if (landingTime < departureTime)
        {
            this.setState({ errorMessage: 'Landing time must be after departure time.'})
            return false;
        }

        const tickets = parseInt(flight.RemainingTickets);
        if (tickets < 0 || tickets > 1000)
        {
            this.setState({ errorMessage: 'Tickets number must be between 0 and 1000.'})
            return false;
        }

        const ticketPrice = parseInt(flight.TicketPrice);
        if (ticketPrice < 0 || ticketPrice > 50000)
        {
            this.setState({ errorMessage: 'Ticket price must be between 0 and 50,000.'})
            return false;
        }

        return true;
    }

    componentDidMount() {
        this.getCountries();
        this.resetFlightState();
    }

    render() {

        if (this.state.countries == 'loading')
        {
            return (
                <img src={loadingIcon} alt="loading..." className="loading-user-view"/>
            )
        }

        if (this.state.countries == 'error')
        {
            return (
                <div className="error-container">
                    <img src={errorIcon} alt="error" className="error-icon"/>
                    <h4 className="error-message">An error occurred while receiving the data. </h4>
                </div>
            )
        }

        let countriesOptions = this.state.countries.map(country => {
            return (
                <option key={country.Id} value={country.Id}>{country.Name}</option> 
            )
        });
        countriesOptions.unshift(<option key={0} value="">{"--Select--"}</option>);

        return (
            <div id="create-flight-container">
                <h2 id="create-flight-heading">Create a New Flight</h2>
                <div id="create-flight">

                    {/* Left Column */}
                    <div id="flight-details-left-column" className="details-column">

                        <div id="select-flight-origin" className="field-cont">
                            <label id="select-flight-origin-label">Origin Country: &nbsp;</label>
                            <select id="select-flight-origin-field" className="form-control" onChange={this.handleChange}
                            value={this.state.flight.OriginCountryCode} name="OriginCountryCode">
                                {countriesOptions}
                            </select>
                        </div>

                        <div id="select-flight-departure" className="field-cont">
                            <label id="select-flight-departure-label">Departure Time: &nbsp;</label>
                            <input type="datetime-local" id="select-flight-departure-field" name="DepartureTime" className="form-control" 
                                value={this.state.flight.DepartureTime} onChange={this.handleChange}></input>
                        </div>

                        <div id="select-flight-tickets-number" className="field-cont">
                            <label id="select-flight-tickets-number-label">Number of Tickets: &nbsp;</label>
                            <input type="text" id="select-flight-tickets-number-field" name="RemainingTickets" className="form-control" 
                                value={this.state.flight.RemainingTickets} onChange={this.handleChange}></input>
                        </div>

                    </div>
                    
                    {/* Right Column */}
                    <div id="air-details-right-column" className="details-column">

                        <div id="select-flight-destination" className="field-cont">
                            <label id="select-flight-destination-label">Destination Country: &nbsp;</label>
                            <select id="select-flight-destination-field" className="form-control" onChange={this.handleChange}
                            value={this.state.flight.DestinationCountryCode} name="DestinationCountryCode">
                                {countriesOptions}
                            </select>
                        </div>

                        <div id="select-flight-landing" className="field-cont">
                            <label id="select-flight-landing-label">Landing Time: &nbsp;</label>
                            <input type="datetime-local" id="select-flight-landing-field" name="LandingTime" className="form-control" 
                                value={this.state.flight.LandingTime} onChange={this.handleChange}></input>
                        </div>

                        <div id="select-flight-ticket-price" className="field-cont">
                            <label id="select-flight-ticket-price-label">Ticket Price: &nbsp;</label>
                            <input type="text" id="select-flight-ticket-price-field" name="TicketPrice" className="form-control" 
                                value={this.state.flight.TicketPrice} onChange={this.handleChange}></input>
                        </div>

                    </div>

                </div>

                <div className="create-flight-buttons">
                    <Button id="clear-fields" text="Clear Fields" icon={cancelIcon} onClick={this.resetFlightState}/>
                    <Button id="create-flight-btn" text="Save" icon={saveIcon} onClick={this.createFlight}/>
                    {this.state.createState != 'loading' &&
                        <div id="create-flight-spacer"/>
                    }
                    {this.state.createState == 'loading' &&
                        <img src={loadingIcon} alt="" class="loading-update-user"/>
                    }
                </div>

                {/* Validation error message: */}
                <SweetAlert 
                    warning
                    show={this.state.errorMessage != ''}
                    showConfirm
                    allowEscape
                    title={this.state.errorMessage}
                    onConfirm={()=>this.setState({errorMessage: ''})}
                    >
                </SweetAlert>

                <SweetAlert 
                    success
                    show={this.state.createState == 'created'}
                    showConfirm
                    allowEscape
                    title="The flight was created and added to the system."
                    onConfirm={() => this.setState({createState: 'none'})}
                    >
                </SweetAlert>

                <SweetAlert 
                    error
                    show={this.state.createState == 'error'}
                    showConfirm
                    allowEscape
                    title="An error occurred while sending the data."
                    onConfirm={() => this.setState({createState: 'none'})}
                    >
                </SweetAlert>

            </div>
        )
    }
}

CreateFlight.contextType = UserContext;

export default CreateFlight;