import React, {Component} from 'react';
import axios from 'axios';
import SweetAlert from 'react-bootstrap-sweetalert';
import AuthService from '../services/AuthService';
import ErrorService from '../services/ErrorService';
import Button from './Button';
import { changeCustomerPasswordURL, changeAirlinePasswordURL } from '../Constants';
import editIcon from '../assets/edit-icon.png';
import saveIcon from '../assets/save-icon.png';
import cancelIcon from '../assets/cancel-icon.png';
import loadingIcon from '../assets/loading-data.gif';
import '../styles/PasswordChange.css';

// This handles the rendering and changing of passwords for the users.
class PasswordChange extends Component {

    state = {
        changeBtnClicked: false,
        currentPassword: '',
        newPassword: '',
        changeState: 'none'
    }

    // Handles the change of the passwords fields.
    handleChange = (e) => {
        this.setState({[e.target.id]: e.target.value});
    }

    // Calls the API to change the password of the user.
    changePassword = () => {

        if (!this.validateFields())
        {
            this.setState({changeState: 'fieldsNotValid'});
            return;
        }

        this.setState({changeState: 'loading'});

        let requestURL;
        if (this.props.userType == 'customer')
            requestURL = changeCustomerPasswordURL;
        else if (this.props.userType == 'airline')
            requestURL = changeAirlinePasswordURL;

        axios({
            method: 'PUT',
            url: requestURL, 
            headers: AuthService.getAuthHeader().headers,
            data: [this.state.currentPassword, this.state.newPassword]
        }).then(() => {          
            this.setState({changeState: 'success'});
            this.props.handlePasswordChange(this.state.newPassword);
        }).catch((error) => {
            ErrorService.logErrorToConsole(error);
            this.setState({ 
                changeState: 'error',
            })
        })
    }

    // Checks if one of the fields are empty.
    validateFields = () => {
        if (this.state.currentPassword != '' && 
            this.state.newPassword != '')
        {
            return true;
        }
        else
        {
            return false;
        }
    }

    // Hides the 'pop-up' form of the password changing.
    closePopUp = () => {
        this.setState({
            changeState: 'none',
            changeBtnClicked: false
        });
    }

    render() {
        return (
            <div className="password-change">
                {/* The password field with the starlets */}
                <label className="password-change-label">Password:&nbsp;</label>
                <div style={{display: 'inline-block'}}>
                    <label className="password-starlets-text">****&nbsp;</label>
                    <img src={editIcon} alt="" className="password-change-icon" onClick={()=>this.setState({changeBtnClicked: true})}/>
                </div>
                {/* The 'pop-up' form where the users fills the old and new password */}
                {this.state.changeBtnClicked && 
                    <div className="popup-container">
                        <div className="change-password-popup">

                            <div className="current-password-cont">
                                <label className="current-password-label">Current Password:&nbsp;</label>
                                <input type="password" id="currentPassword" className="current-password-textbox" onChange={this.handleChange}/>
                            </div>
                            
                            <div className="new-password-cont">
                                <label className="new-password-label">New Password:&nbsp;</label>
                                <input type="password" id="newPassword" className="new-password-textbox" onChange={this.handleChange}/>
                            </div>

                            <div className="change-password-buttons">
                                <Button id="cancel-change-password" text="Cancel" icon={cancelIcon} onClick={()=>this.setState({changeBtnClicked: false})}/>
                                <Button id="confirm-change-password" text="Save" icon={saveIcon} onClick={this.changePassword}/>
                                {this.state.changeState == 'loading' &&
                                    <img src={loadingIcon} alt="loading..." className="password-change-loading"/>
                                }
                            </div>

                        </div>
                    </div>
                }

                <SweetAlert 
                    success
                    show={this.state.changeState == 'success'}
                    showConfirm
                    allowEscape
                    title="Your password is now changed"
                    onConfirm={this.closePopUp}
                    >
                </SweetAlert>

                <SweetAlert 
                    warning
                    show={this.state.changeState == 'fieldsNotValid'}
                    showConfirm
                    allowEscape
                    title="All fields must be filled"
                    onConfirm={() => this.setState({changeState: 'none'})}
                    >
                </SweetAlert>

                <SweetAlert 
                    error
                    show={this.state.changeState == 'error'}
                    showConfirm
                    allowEscape
                    title="An error occurred while changing you password"
                    onConfirm={() => this.setState({changeState: 'none'})}
                    >
                </SweetAlert>

            </div>
        )
    }
}

export default PasswordChange;