import React, { Component } from "react";
import AuthService from './services/AuthService';

export const UserContext = React.createContext();

// This context provider holds and sets the information about the user.
class UserContextProvider extends Component {
    
    state = {
        role: '',
        name: ''
    }

    // Sets the user's info from the login token that stored in the browser.
    setUserInfo = () => {
      this.setState(AuthService.getUserInfo)
    }
    
    // Gets a new login token from the server with the updated user info
    // and sets it to the context's state.
    updateUserInfo = async (credentials) => {
      let answer = await AuthService.login(credentials);
      if (answer == 'success')
      {
        this.setUserInfo();
        return true;
      }
      else
      {
        return false;
      }
    }

    componentDidMount() {
      this.setUserInfo();
    }

    render() {
      if (this.state.role == '')
      {
        return null;
      }
      else
      {
        return (
            <UserContext.Provider value={{
              userInfo: this.state, 
              handleLogin: this.setUserInfo,
              updateUserInfo: this.updateUserInfo
              }}>
                {this.props.children}
            </UserContext.Provider>
        );
      }
    }
}

export default UserContextProvider;
