import React, { Component } from 'react';
import { Redirect } from 'react-router-dom';
import axios from 'axios';
import SweetAlert from 'react-bootstrap-sweetalert';
import AuthService from '../services/AuthService';
import ErrorService from '../services/ErrorService';
import DateTimeService from '../services/DateTimeService';
import { UserContext } from '../UserContext';
import { getFlightByIdURL, purchaseTicketURL } from '../Constants';
import loadingData from '../assets/loading-data.gif';
import errorIcon from '../assets/error-icon.png';
import '../styles/PurchaseTicket.css';

// This is the flight ticket(s) purchase.
class PurchaseTicket extends Component {

    // A user can buy a ticket for a single flight (one ticket, one way) or
    // buy two tickets at once (back and forth).

    state = {
        ticketType: '',
        departureFlight: '',
        returnFlight: '',
        flightsState: 'loading',
        purchaseState: 'none'
    }

    // This component gets the ticket information from the URL query.
    // That method handles it and updates the state.
    getFlightIdsFromUrl = () => {
        let queryString = this.props.history.location.search;
        let queryBlocks = queryString.split('&');
        let queryValues = queryBlocks.map((block) => {
            return block.split('=')[1];
        });

        this.setState({ ticketType: queryValues[0] });

        if (queryValues[0] == 'one-way') 
        {
            this.getFlights(queryValues[1]);
        }
        else 
        {
            this.getFlights(queryValues[1], queryValues[2]);
        }
    }

    // Gets the flight(s) information to display it on the page.
    getFlights = async (departureFlightId, returnFlightId) => {

        await axios.get(getFlightByIdURL + '/' + departureFlightId
        ).then((response) => {
            this.setState({
                departureFlight: response.data
            })
        }).catch((error) => {
            ErrorService.logErrorToConsole(error);
            this.setState({
                flightsState: 'error'
            })
        })

        // If its a roundtrip ticket (two flights) it will get the second one also.
        if (returnFlightId != null) 
        {
            await axios.get(getFlightByIdURL + '/' + returnFlightId
            ).then((response) => {
                this.setState({
                    returnFlight: response.data
                })
            }).catch((error) => {
                ErrorService.logErrorToConsole(error);
                this.setState({
                    flightsState: 'error'
                })
            })
        }

        this.setState({
            flightsState: 'loaded'
        })
    }

    // Handles the procedure of buying a ticket.
    buyTicket = async () => {

        if (!this.isRegisteredCustomer())
            return;

        this.setState({purchaseState: 'loading'})

        await axios({
            method: 'POST',
            url: purchaseTicketURL + '/' + this.state.departureFlight.Id, 
            headers: AuthService.getAuthHeader().headers,
        }).then(() => {
        }).catch((error) => {
            ErrorService.logErrorToConsole(error);
            try
            {
                // Handling double ticket exception.
                if (error.response.data.ExceptionType == 'FlightCenter.OneTicketForCustomerOnlyException')
                    this.setState({purchaseState: 'doubleTicketError'});
                else
                    this.setState({purchaseState: 'error'});
            }
            catch
            {
                this.setState({purchaseState: 'error'});
            }
        })

        // If its a roundtrip ticket (two flights) it will purchase the second one also.
        if (this.state.returnFlight != '')
        {
            await axios({
                method: 'POST',
                url: purchaseTicketURL + '/' + this.state.returnFlight.Id, 
                headers: AuthService.getAuthHeader().headers,
            }).then(() => {
            }).catch((error) => {
                ErrorService.logErrorToConsole(error);
                try
                {
                    if (error.response.data.ExceptionType == 'FlightCenter.OneTicketForCustomerOnlyException')
                        this.setState({purchaseState: 'doubleTicketError'});
                    else
                        this.setState({purchaseState: 'error'});
                }
                catch
                {
                    this.setState({purchaseState: 'error'});
                }
            })
        }

        setTimeout(() => {
            if (this.state.purchaseState == 'loading')
                this.setState({purchaseState: 'done'})
        },100)    
    }

    // Checks if the current user is a registered customer.
    isRegisteredCustomer = () => {

        if (this.context.userInfo.role == "Customer")
        {
            return true;
        }
        else
        {
            this.setState({purchaseState: 'notRegistered'});
            return false;
        }
    }

    // This function is triggered when an anonymous user is detected.
    // It saves the ticket details in the browser, so the user will
    // be able to continue this purchase after the login or registration.
    saveTicketForLater = () => {

        let ticket = {
            ticketType: this.state.ticketType,
            departureFlight: this.state.departureFlight.Id,
            returnFlight: this.state.returnFlight.Id
        }

        localStorage.setItem("ticket", JSON.stringify(ticket));

        this.setState({purchaseState: 'redirectToLoginPage'});
    }
    
    componentDidMount() {
        this.getFlightIdsFromUrl();
    };

    render() {

        if (this.state.flightsState == 'loading' || this.state.purchaseState == 'loading') 
        {
            return (
                <img src={loadingData} alt="" id="loading-ticket-data" />
            )
        }

        if (this.state.flightsState == 'error') 
        {
            return (
                <div className="page-content-container">
                    <div className="error-container">
                        <img src={errorIcon} alt="error" className="error-icon" />
                        <h4 className="error-message">An error occurred while receiving the data. </h4>
                    </div>
                </div>
            )
        }

        // Shorthands:
        let depFlight = this.state.departureFlight;
        let retFlight;

        // UI Elements that depends on the type of the ticket:
        let depFlightHeading;
        let roundtripTicketPrice;
        let flightReference;
        let doubleTicketMessage;

        if (this.state.ticketType == 'one-way') 
        {
            depFlightHeading = 'Flight Details:';
            flightReference = 'flight';
            doubleTicketMessage = 'You have already bought a ticket for this flight. '
        }
        else 
        {
            depFlightHeading = 'Deparutre Flight:';
            retFlight = this.state.returnFlight;
            roundtripTicketPrice = parseInt(depFlight.TicketPrice) + parseInt(retFlight.TicketPrice);
            flightReference = 'flights';
            doubleTicketMessage = 'You have already bought a ticket for one or both of these flights. '
        }

        return (
            <div id="purchase-ticket-container" className="page-content-container">

                <h2 id="purchase-heading">Purchase Ticket</h2>

                {/* The departure ticket details */}
                <div className="ticket-details" id="dept-flight-details">

                    <h4 className="flight-heading">{depFlightHeading}</h4>

                    <div id="flight-ticket-airline-cont" className="ticket-field-cont">
                        <label id="flight-ticket-airline" className="label">Airline Company: &nbsp;</label>
                        <label id="flight-ticket-airline" className="text">{depFlight.AirlineCompany}</label>
                    </div>
                    <div id="flight-ticket-origin-cont" className="ticket-field-cont">
                        <label id="flight-ticket-origin" className="label">From: &nbsp;</label>
                        <label id="flight-ticket-origin" className="text">{depFlight.OriginCountry}</label>
                    </div>
                    <div id="flight-ticket-destination-cont" className="ticket-field-cont">
                        <label id="flight-ticket-destination" className="label">To: &nbsp;</label>
                        <label id="flight-ticket-destination" className="text">{depFlight.DestinationCountry}</label>
                    </div>
                    <div id="flight-ticket-departure-cont" className="ticket-field-cont">
                        <label id="flight-ticket-departure" className="label">Departure: &nbsp;</label>
                        <label id="flight-ticket-departure" className="text">{DateTimeService.getFormattedDateTimeString(depFlight.DepartureTime)}</label>
                    </div>
                    <div id="flight-ticket-landing-cont" className="ticket-field-cont">
                        <label id="flight-ticket-landing" className="label">Landing: &nbsp;</label>
                        <label id="flight-ticket-landing" className="text">{DateTimeService.getFormattedDateTimeString(depFlight.LandingTime)}</label>
                    </div>
                    {this.state.ticketType == 'roundtrip' &&
                        <div id="flight-ticket-price-cont" className="ticket-field-cont">
                            <label id="flight-ticket-price" className="label">Ticket Price: &nbsp;</label>
                            <label id="flight-ticket-price" className="text">{depFlight.TicketPrice}$</label>
                        </div>
                    }

                </div>

                {/* The return ticket details (if exists)*/}
                {this.state.ticketType == 'roundtrip' &&
                    <div style={{display: 'inline-block'}}>

                        <div className="tickets-spacer"/>

                        <div className="ticket-details" id="ret-flight-details">

                            <h4 className="flight-heading">Return Flight:</h4>

                            <div id="flight-ticket-airline-cont" className="ticket-field-cont">
                                <label id="flight-ticket-airline" className="label">Airline Company: &nbsp;</label>
                                <label id="flight-ticket-airline" className="text">{retFlight.AirlineCompany}</label>
                            </div>
                            <div id="flight-ticket-origin-cont" className="ticket-field-cont">
                                <label id="flight-ticket-origin" className="label">From: &nbsp;</label>
                                <label id="flight-ticket-origin" className="text">{retFlight.OriginCountry}</label>
                            </div>
                            <div id="flight-ticket-destination-cont" className="ticket-field-cont">
                                <label id="flight-ticket-destination" className="label">To: &nbsp;</label>
                                <label id="flight-ticket-destination" className="text">{retFlight.DestinationCountry}</label>
                            </div>
                            <div id="flight-ticket-departure-cont" className="ticket-field-cont">
                                <label id="flight-ticket-departure" className="label">Departure: &nbsp;</label>
                                <label id="flight-ticket-departure" className="text">{DateTimeService.getFormattedDateTimeString(retFlight.DepartureTime)}</label>
                            </div>
                            <div id="flight-ticket-landing-cont" className="ticket-field-cont">
                                <label id="flight-ticket-landing" className="label">Landing: &nbsp;</label>
                                <label id="flight-ticket-landing" className="text">{DateTimeService.getFormattedDateTimeString(retFlight.LandingTime)}</label>
                            </div>

                            <div id="flight-ticket-price-cont" className="ticket-field-cont">
                                <label id="flight-ticket-price" className="label">Ticket Price: &nbsp;</label>
                                <label id="flight-ticket-price" className="text">{retFlight.TicketPrice}$</label>
                            </div>
                        </div>
                    </div>
                }
                {/* Purchase buttons */}
                {this.state.ticketType == 'one-way' &&
                    <div id="buy-ticket-btn" onClick={this.buyTicket}>Click here to buy this ticket for {depFlight.TicketPrice}$</div>
                }
                {this.state.ticketType == 'roundtrip' &&
                    <div id="buy-ticket-btn" onClick={this.buyTicket}>Click here to buy this roundtrip ticket for {roundtripTicketPrice}$</div>
                }
                {/* Error message */}
                {this.state.purchaseState == 'error' &&
                    <label id="purchase-error">An error occurred while processing the purchase.</label>
                }       

                {/* When the purchase is completed, this alert will be shown */}
                <SweetAlert 
                    success
                    show={this.state.purchaseState == 'done'}
                    showConfirm
                    allowEscape
                    title="Purchase Completed"
                    onConfirm={() => this.setState({purchaseState: 'redirectToUserPage'})}
                    >
                    Thank you for booking your {flightReference} with us!
                </SweetAlert>

                {/* The user will be rediricted to the user page after the purchase ends */}
                {this.state.purchaseState == 'redirectToUserPage' &&
                    <Redirect to='/user-page'/>
                } 

                {/* An alert to inform the customer he cannot buy two tickets for the same flight */}
                <SweetAlert 
                    warning
                    show={this.state.purchaseState == 'doubleTicketError'}
                    showConfirm
                    allowEscape
                    title="Purchase declined"
                    onConfirm={() => this.setState({purchaseState: 'none'})}
                    >
                    {doubleTicketMessage}
                    You cannot buy two tickets for the same flight. We are sorry for the inconvenience.
                </SweetAlert>

                {/* An alert for an unregistered user */}
                <SweetAlert 
                    show={this.state.purchaseState == 'notRegistered'}
                    showConfirm
                    allowEscape
                    title="Hello Guest!"
                    onConfirm={() => this.saveTicketForLater()}
                    >
                    Please login or register in order to buy a ticket. You will now be redirected to the login page.
                </SweetAlert>

                {/* After the alert above, the user will be redirected to the login page */}
                {this.state.purchaseState == 'redirectToLoginPage' &&
                    <Redirect to='/login'/>
                } 

            </div>
        )
    }
}

PurchaseTicket.contextType = UserContext;

export default PurchaseTicket;