import axios from 'axios';
import JwtDecode from 'jwt-decode';
import ErrorService from './ErrorService';
import { getAuthTokenURL } from '../Constants';

// This service handles the login, logout and the JWT login token.
class AuthService {

    // Sends the login credentials to the API to make the login trial
    // if succeeds, It saves the login token in the browser.
    login = (credentials) => {
        return axios.post(getAuthTokenURL, credentials).then(res => {
            // Login success
            if (res.status == 200)
            {
                localStorage.setItem("userToken", res.data);
                return "success";
            }
        }).catch(error => {
            
            // Login denied (wrong credentials)
            if (error.response != null)
            {
                if (error.response.status == 401)
                {
                    return "failure";
                }
            }
            
            // Any other error from the server
            ErrorService.logErrorToConsole(error);
            return "error";
        })
    }

    // Gets the information of the user from the JWT login token.
    getUserInfo = () => {
        let userToken = localStorage.getItem("userToken");
        let anonymousUser = { role: 'Anonymous', name: 'Guest' }

        // Anonymous user
        if (userToken == null)
        {
            return anonymousUser;
        }
        // Registered user
        else
        {
            let user = JwtDecode(userToken);
            let userInfo = JSON.parse(user.LoginToken);
            let name = this.getUserName(user.role, userInfo);
            
            // If the token is expired it will be removed from the browser.
            if (this.checkToken(user.exp) == false)
            {
                localStorage.removeItem("userToken");
                return anonymousUser;
            }
            else
            {
                return {role: user.role, 
                        name: name, 
                        user: userInfo.User};
            }
        }
    }

    // Detemines what 'display name' to use for each user type.
    getUserName = (userRole, userInfo) => {
        switch (userRole) {
            case 'Administrator':
            return 'Admin';
            
            case 'Customer':
            return userInfo.User.FirstName;

            case 'AirlineCompany':
            return userInfo.User.Name;

            default:
            return 'Unknown';
        }
    }

    // Returns the authorization header for the AJAX calls.
    getAuthHeader = () => {
        return {
            headers: {
                Authorization: 'Bearer ' + localStorage.getItem("userToken"),
            }
        };
    }

    // Checks if the token is not expired.
    checkToken = (expireTimestamp) => {
        let expireDate = new Date(expireTimestamp * 1000);
        return expireDate > new Date();
    }

    // Handles the logout opertaion.
    logout = (handleUserChange) => {
        localStorage.removeItem("userToken");
        handleUserChange();
    }
}

export default new AuthService();