import React, {Component} from 'react';
import axios from 'axios';
import SweetAlert from 'react-bootstrap-sweetalert';
import AuthService from '../../services/AuthService';
import ErrorService from '../../services/ErrorService';
import Button from '../../components/Button';
import HiddenField from '../HiddenField';
import { updateCustomerURL } from '../../Constants';
import customerIcon from '../../assets/customer-icon.png';
import arrowsIcon from '../../assets/arrows-icon.png';
import editIcon from '../../assets/edit-icon.png';
import deleteIcon from '../../assets/delete-icon.png';
import saveIcon from '../../assets/save-icon.png';
import cancelIcon from '../../assets/cancel-icon.png';
import loadingIcon from '../../assets/loading-data.gif';

// This is a child component of the ManageCustomers component.
// It displays all the information about the customer and allows the user to update it or
// delete the customer.
class CustomerContainer extends Component {

    state = {
        customer: this.props.customer,
        newCustomer: this.props.customer,
        moreDetails: false,
        editCustomer: false,
        updateState: 'none',
        validationMessage: '',
        deleteState: 'none'
    }

    // The moreDetails state changes when the user click the "more details" button. 
    // When true, the component will present more information of the customer - username, password, address etc...
    toggleMoreDetails = () => {
        this.setState({moreDetails: !this.state.moreDetails});
    }

    // This state sets the component to edit mode.
    showEditCustomer = () => {
        this.setState({editCustomer: true});
    }

    // When the user types any changes to the fields in edit mode, it will handle it.
    // All the changes will be reflected on the 'newCustomer' state, while the regular 'customer' state
    // Will stay the same.
    handleChange = (e) => {
        let customerClone = Object.assign({}, this.state.newCustomer);
        customerClone[e.target.id] = e.target.value;
        this.setState({
            newCustomer: customerClone
        })
        // Removes the validation error message, if there is one.
        this.setState({ 
            validationMessage: '',
        })
    }

    // This aborts the editing mode.
    cancelEdit = () => {
        this.setState({
            editCustomer: false,
            newCustomer: this.state.customer
        });
    }

    // Updates the customer with the new information when the user clicks the 'save' button.
    updateCustomer = () => {

        if (!this.validateCustomerDetails(this.state.newCustomer))
        {
            this.setState({ 
                validationMessage: 'All fields must be filled.',
            })
            return;
        }

        this.setState({ 
            updateState: 'loading',
        })
        axios({
            method: 'PUT',
            url: updateCustomerURL, 
            headers: AuthService.getAuthHeader().headers,
            data: this.state.newCustomer
        }).then(() => {
            this.setState({ 
                customer: this.state.newCustomer,
                updateState: 'updated',
                editCustomer: false,
                moreDetails: true
            })
        }).catch((error) => {
            ErrorService.logErrorToConsole(error);
            this.setState({ 
                updateState: 'error',
            })
        })
    }

    // Validates that none of the customer's fields are empty before updating.
    validateCustomerDetails = (customer) => {
        if (customer.FirstName == '' ||
            customer.LastName == '' ||
            customer.Email == '' ||
            customer.UserName == '' ||
            customer.Address == '' ||
            customer.PhoneNo == '' )
        {
            return false;
        }
        else
            return true;
    }

    // Present an alert for the user to confirm the delete operation.
    handleDeleteBtn = () => {
        this.setState({deleteState: 'deleteAlert'})
    }

    // When the user confirms to delete, this funcion will invoke the deletion function that
    // was recieved from the parent component, and then the parent will delete the customer.
    handleDeleteCustomer = () => {
        this.setState({ 
            deleteState: 'deleting' 
        });
        this.props.deleteFunc(this.state.customer, this.deleteErrorHandle);
    }

    // When an error occurrs while deleting the customer.
    deleteErrorHandle = () => {
        this.setState({deleteState: 'error'})
    }

    render() {

        // Every customer's icon has a different color by using this style.
        const pictureStyle = { filter: `hue-rotate(${this.props.colorKey*50}deg)` }

        const customer = this.state.customer;

        let customerFullName = `${customer.FirstName} ${customer.LastName}`

        if (this.state.deleteState == 'deleting')
        {
            return (
                <div className="customer-cont">
                    <img src={loadingIcon} alt="" className="loading-delete-element"/>
                </div>
            )
        }
        
        else if (!this.state.editCustomer)
        {
            return (
                <div className="customer-cont">
                    {/* This will render when the component mounts on the first time.
                        The basic customer details and the action buttons */}
                    <div className="customer-first-row">
                        <img src={customerIcon} alt="" className="customer-icon" style={pictureStyle}/>
                        <label className="customer-id">{customer.Id}</label>
                        <label className="customer-name">{customerFullName}</label>
                        <label className="customer-email">{customer.Email}</label>

                        <Button id="more-details" onClick={this.toggleMoreDetails} text="More Details" icon={arrowsIcon}/>

                        <Button id="edit" onClick={this.showEditCustomer} text="Edit" icon={editIcon}/>

                        <Button id="delete" onClick={this.handleDeleteBtn} text="Delete" icon={deleteIcon}/>
                    </div>
                    {/* When the user clicks "More Details" this will apear: */}
                    { this.state.moreDetails &&

                        <div className="customer-second-row">
                            <div className="customer-username-cont">
                                <label className="customer-username-label">Username:&nbsp;</label>
                                <label className="customer-username">{customer.UserName}</label>
                            </div>

                            <HiddenField id="customer-password" label="Password:" value={customer.Password}/>

                            <div className="customer-address-cont">
                                <label className="customer-address-label">Address:&nbsp;</label>
                                <label className="customer-address">{customer.Address}</label>
                            </div>
                            <div className="customer-phone-cont">
                                <label className="customer-phone-label">Phone number:&nbsp;</label>
                                <label className="customer-phone">{customer.PhoneNo}</label>
                            </div>
                        </div>
                    }
                    {/* An alert before removing the customer: */}
                    <SweetAlert 
                        show={this.state.deleteState == 'deleteAlert'}
                        showCancel
                        showConfirm
                        allowEscape
                        title="Confirm operation"
                        onConfirm={this.handleDeleteCustomer}
                        onCancel={()=>{this.setState({deleteState: 'none'})}}
                        >
                        {`Are you sure you want to delete customer ${this.state.customer.Id}?`}
                    </SweetAlert>
                    
                    {this.state.deleteState == 'error' &&
                        <label id="customer-action-error">An error occurred while removing the customer.</label>
                    }
                </div>
            )
        }
        else // Edit mode:
        {
            return (
                <div className="customer-cont">
                    <div className="customer-first-row">

                        <img src={customerIcon} alt="" className="customer-icon" style={pictureStyle}/>
                        <label className="customer-id">{customer.Id}</label>
                        <input type="text" className="customer-name" id="FirstName" onChange={this.handleChange} defaultValue={customer.FirstName}/>
                        <input type="text" className="customer-name" id="LastName" onChange={this.handleChange} defaultValue={customer.LastName}/>
                        <input type="text" className="customer-email" id="Email" onChange={this.handleChange} defaultValue={customer.Email}/>

                    </div>

                    <div className="customer-second-row">
                        <div className="customer-username-cont">
                            <label className="customer-username-label">Username:&nbsp;</label>
                            <input type="text" className="customer-username" id="UserName" onChange={this.handleChange} defaultValue={customer.UserName}/>
                        </div>

                        <div className="customer-address-cont">
                            <label className="customer-address-label">Address:&nbsp;</label>
                            <input type="text" className="customer-address" id="Address" onChange={this.handleChange} defaultValue={customer.Address}/>
                        </div>

                        <div className="customer-phone-cont">
                            <label className="customer-phone-label">Phone number:&nbsp;</label>
                            <input type="text" className="customer-phone" id="PhoneNo" onChange={this.handleChange} defaultValue={customer.PhoneNo}/>
                        </div>
                    </div>

                    <div className="customer-third-row">

                        <Button id="cancel-edit-cust" onClick={this.cancelEdit} text="Cancel" icon={cancelIcon}/>

                        <Button id="save-cust" onClick={this.updateCustomer} text="Save" icon={saveIcon}/>

                        <label className="validation-message">{this.state.validationMessage}</label>

                        {this.state.updateState == 'loading' &&
                            <img src={loadingIcon} alt="" className="loading-update-user"/>
                        }
                        {this.state.updateState == 'error' &&
                            <label id="customer-action-error">An error occurred while updating the data.</label>
                        }
                    </div>
                </div>
            )
        }
    }
}

export default CustomerContainer;