import React from 'react';
import { AirlineRegisterURL } from '../Constants';

// This component contains the airline company register page.
// NOTICE: The airline company register page is an external page. It is being presented here inside an iframe element.
const AirlineRegister = function(props) {

    return (
        <div id="airline-register-container" className="page-content-container">
            <iframe id="customer-register-frame" src={AirlineRegisterURL} className="iframe-cont" title="airline-register"></iframe>
        </div>
    )
}

export default AirlineRegister;