import React from 'react';
import { CustomerRegisterURL } from '../Constants';

// This component contains the customer register page.
// NOTICE: The customer register page is an external page. It is being presented here inside an iframe element.
const CustomerRegister = function(props) {

    return (
        <div id="customer-register-container" className="page-content-container">
            <iframe id="customer-register-frame" src={CustomerRegisterURL} className="iframe-cont" title="customer-register"></iframe>
        </div>
    )
}

export default CustomerRegister;