import React, {Component} from 'react';
import {NavLink, Redirect} from 'react-router-dom';
import ViewService from '../services/ViewService';
import Button from './Button';
import UserActions from './UserActions';
import logo from '../assets/logo.png';
import menuIcon from '../assets/menu-icon.png';
import closeIcon from '../assets/close-icon.png';

// This is the top bar, containing the website's logo, the navigation links and the UserAction component.
class Navbar extends Component {

    state = {
        mobileMenuOpened: false,
        homePageClicked: false
    }

    goToHomePage = () => {
        this.setState({homePageClicked: true})
        setTimeout(()=> this.setState({homePageClicked: false}), 100)
    }

    toggleMenu = () => {
        this.setState({
            mobileMenuOpened: !this.state.mobileMenuOpened
        })
    }

    // When the login menu opens, it closes the navigation menu.
    userMenuOpened = () => {
        if (this.state.mobileMenuOpened)
        {
            this.setState({mobileMenuOpened: false});
        }
    }

    render() {
        const navActiveStyle = {
            textShadow:'0.5px 0.5px 2px'
        }
        
        return (
            <nav>
                <div className="top-navbar">

                    { this.state.homePageClicked &&
                    <Redirect to="/"/>
                    }

                    <img src={logo} alt="" href="/" className="site-logo" onClick={this.goToHomePage}></img>
                    {!ViewService.isMobileView() && // Desktop view
                        <div className="nav-options">
                            <NavLink className="nav-option" activeStyle={navActiveStyle} exact to="/">Home</NavLink>
                            <NavLink className="nav-option" activeStyle={navActiveStyle} to="/flights">Flights</NavLink>
                            <NavLink className="nav-option" activeStyle={navActiveStyle} to="/deals">Deals</NavLink>
                            <NavLink className="nav-option" activeStyle={navActiveStyle} to="/flights-board">Flights Board</NavLink>
                            <NavLink className="nav-option" activeStyle={navActiveStyle} to="/about">About</NavLink>
                        </div>
                    }
                    {(ViewService.isMobileView() && !this.state.mobileMenuOpened) && // Mobile view
                        <Button id="mobile-nav-menu-btn" onClick={this.toggleMenu} text="Menu" icon={menuIcon}/>

                    }

                    {(ViewService.isMobileView() && this.state.mobileMenuOpened) && // Nav menu opened

                        <div>
                            <Button id="mobile-nav-menu-btn" onClick={()=>this.setState({mobileMenuOpened: !this.state.mobileMenuOpened})} text="Menu" icon={menuIcon}/>
                            <div id="mobile-nav-menu" className="mobile-menu">
                                <NavLink onClick={this.toggleMenu} className="mobile-nav-option" activeStyle={navActiveStyle} exact to="/">Home</NavLink>
                                <NavLink onClick={this.toggleMenu} className="mobile-nav-option" activeStyle={navActiveStyle} to="/flights">Flights</NavLink>
                                <NavLink onClick={this.toggleMenu} className="mobile-nav-option" activeStyle={navActiveStyle} to="/deals">Deals</NavLink>
                                <NavLink onClick={this.toggleMenu} className="mobile-nav-option" activeStyle={navActiveStyle} to="/flights-board">Flights Board</NavLink>
                                <NavLink onClick={this.toggleMenu} className="mobile-nav-option" activeStyle={navActiveStyle} to="/about">About</NavLink>
                                <Button id="close-menu-btn" onClick={this.toggleMenu} text="Close" icon={closeIcon}/>
                            </div>
                        </div>
                    }

                    <UserActions navMenuOpen={this.state.mobileMenuOpened} userMenuOpened={this.userMenuOpened}/>
                </div>
            </nav>
        );
    }
}

export default Navbar;