import React, {Component} from 'react';
import {Redirect, NavLink} from 'react-router-dom';
import SweetAlert from 'sweetalert2-react';
import AuthService from '../services/AuthService';
import loadingData from '../assets/loading-data.gif';
import {UserContext} from '../UserContext';
import '../styles/Login.css';

// This is the login page.
class Login extends Component {

    state = {
        username: '',
        password: '',
        loginState: 'none',
        message: ''
    }

    // Handles the login procedure.
    login = async (e) => {
        e.preventDefault();
        const credentials = [this.state.username, this.state.password];
        this.setState({loginState:'loading'});
        let loginStatus = await AuthService.login(credentials);
        if (loginStatus == "success")
        {
            setTimeout(()=>{this.setState({loginState:'success'})},200);
            this.context.handleLogin();
        }
        else if (loginStatus == "failure")
        {
            this.setState({
                loginState:'failure',
                message: 'Wrong username or password inserted.'
            });
        }
        else if (loginStatus == "error")
        {
            this.setState({
                loginState:'error',
                message: 'An error occurred while connecting to the server.'
            });
        }
    }

    // Handles the changes of the credentials fields.
    handleChange = (e) => {
        this.setState({ 
            [e.target.name]: e.target.value 
        });
    }

    render() {

        return (
            <div id="login-container" className="page-content-container">
                <h2>Login</h2>
                <div id="login-form">
                    <form onSubmit={this.login}>
                        <label id="username-label">Username:</label>
                        <input type="text" name="username" id="username" className="form-control" onChange={this.handleChange} required></input>
                        <label ud="password-label">Password:</label>
                        <input type="password" name="password" id="password" className="form-control" onChange={this.handleChange} required></input>
                        <button type="submit" className="btn btn-primary" id="login-btn">Submit</button>

                        {this.state.loginState == 'loading' &&
                            <img src={loadingData} alt="loading..." id="loading-login"/>
                        }
                        <label id="login-message">{this.state.message}</label>
                    </form>
                </div>
                <div id="sign-up-container">
                    <label id="new-user">Don't have an account yet?&nbsp;</label>
                    <NavLink to="/register">Sign Up</NavLink>
                    <label>.</label>
                </div>
                {/* When the user logs in successfully, a redirect element will send him to the user page. */}
                {this.state.loginState == 'userConfirmed' &&
                    <Redirect to='/user-page'/>
                }
                <SweetAlert
                    success
                    show={this.state.loginState == 'success'}
                    title={`Welcome back ${this.context.userInfo.name}!`}
                    text="We are glad to have you here again."
                    onConfirm={() => this.setState({ loginState: 'userConfirmed' })}
                    />
            </div>

        );
    }
}

Login.contextType = UserContext;

export default Login;