import React, {Component} from 'react';
import SweetAlert from 'react-bootstrap-sweetalert';
import CountriesService from '../../services/CountriesService';
import ViewService from '../../services/ViewService';
import Button from '../../components/Button';
import HiddenField from '../HiddenField';
import airlineIcon from '../../assets/airline-icon.png';
import arrowsIcon from '../../assets/arrows-icon.png';
import removeIcon from '../../assets/delete-icon.png';
import aproveIcon from '../../assets/save-icon.png';
import loadingIcon from '../../assets/loading-data.gif';

// This is a child component of the AdminInbox component.
// It presents all the information about the airline company and allows the user to
// aprove its registration or remove it.
class PendingAirline extends Component {

    state = {
        airline: this.props.airline,
        countries: [],
        moreDetails: false,
        showPassword: false,
        actionState: 'none',
        alertMessage: '',
        errorState: false
    }

    // Gets all the countries from the server.
    getCountries = async () => {

        let countries = await CountriesService.getCountries();
        if (countries != null)
        {
            this.setState({
                countries: countries,
            });
        }
        else
        {
            this.setState({ errorState: true})
        }
    }

    // When the moreDetails state is true, the component will show more information of the airline: 
    // username, password and origin country.
    toggleMoreDetails = () => {
        this.setState({moreDetails: !this.state.moreDetails});
    }

    // When the showPassword state is true, the component reveals the airline's password.
    toggleShowPassword = () => {
        this.setState({showPassword: !this.state.showPassword});
    }

    // When the user confirms to remove, this funcion will invoke the removal function that
    // was recieved from the parent component, and then the parent will delete the airline.
    handleRemoveAirline = () => {
        this.setState({ 
            actionState: 'deleting' 
        });
        this.props.removeFunc(this.state.airline, this.handleOperationError);
    }

    // When the user confirms to aprove, this funcion will invoke the aproval function that
    // was recieved from the parent component, and then the parent will remove the airline from the list.
    handleAproveAirline = () => {
        this.setState({ 
            actionState: 'aproving' 
        });
        this.props.aproveFunc(this.state.airline, this.handleOperationError);
    }

    // Shows an alert for the user to confirm the aprove operation.
    showAproveAlert = () => {
        this.setState({
            actionState: 'aproveAlert',
            alertMessage: `Aprove '${this.state.airline.Name}' and create a new account for it?`
        })
    }

    // Shows an alert for the user to confirm the remove operation.
    showRemoveAlert = () => {
        this.setState({
            actionState: 'deleteAlert',
            alertMessage: `Reject '${this.state.airline.Name}' and remove it from the list?`
        })
    }

    // When an error occurrs while deleting or aproving the airline.
    handleOperationError = () => {
        this.setState({actionState: 'error'})
    }

    getCountryName = (id) => {
        let country = this.state.countries.find(country => {
            return country.Id == id;
        })
        return country.Name;
    }

    componentDidMount() {
        this.getCountries();
    }

    render() {

        // Every airline's icon has a different color by using this style.
        const pictureStyle = { filter: `hue-rotate(${this.props.colorKey*50}deg)` }

        const airline = this.state.airline;

        if (this.state.countries.length == 0)
        {
            return (
                <img src={loadingIcon} alt="" className="loading-user-view"/>
            )
        }

        if (this.state.errorState == true)
        {
            return (
                <div className="airline-cont">
                    <label className="airline-error">An error occurred while displaying this airline's data.</label>
                </div>
            )
        }

        if (this.state.actionState == 'deleting')
        {
            return (
                <div className="airline-cont">
                    <img src={loadingIcon} alt="" className="loading-delete-element"/>
                </div>
            )
        }

        return (
            <div className="airline-cont">

                {!ViewService.isMobileView() && // Desktop view
                    <div>
                        {/* These details will render when the component mounts on the first time.
                        The basic airline details and the action buttons */}
                        <div className="airline-first-row">
                            <img src={airlineIcon} alt="" className="airline-icon" style={pictureStyle}/>
                            <label className="airline-name">{airline.Name}</label>
                            <label className="airline-email">{airline.Email}</label>
                            
                            <Button id="more-details" onClick={this.toggleMoreDetails} text="More Details" icon={arrowsIcon}/>

                            <Button id="aprove" onClick={this.showAproveAlert} text="Aprove" icon={aproveIcon}/>

                            <Button id="remove-airline" onClick={this.showRemoveAlert} text="Remove" icon={removeIcon}/>

                            {(this.state.actionState == 'deleting' || this.state.actionState == 'aproving') &&
                                <img src={loadingIcon} alt="" className="loading-update-user"/>
                            }

                        </div>
                        {/* When the user clicks "More Details" this will apear: */}
                        { this.state.moreDetails &&

                            <div className="airline-second-row">
                                <div className="airline-username-cont">
                                    <label className="airline-username-label">Username:&nbsp;</label>
                                    <label className="airline-username">{airline.UserName}</label>
                                </div>
                                
                                <HiddenField id="airline-password" label="Password:" value={airline.Password}/>

                                <div className="airline-country-cont">
                                    <label className="airline-country-label">Country:&nbsp;</label>
                                    <label className="airline-country">{this.getCountryName(this.state.airline.CountryCode)}</label>
                                </div>
                            </div>
                        }
                    </div>
                }

                {ViewService.isMobileView() && // Mobile view
                    <div>
                        <img src={airlineIcon} alt="" className="airline-icon" style={pictureStyle}/>
                        <label className="airline-name">{airline.Name}</label>
                        <label className="airline-email">{airline.Email}</label>
                        
                        <div className="airline-username-cont">
                            <label className="airline-username-label">Username:&nbsp;</label>
                            <label className="airline-username">{airline.UserName}</label>
                        </div>

                        <HiddenField id="airline-password" label="Password:" value={airline.Password}/>

                        <div className="airline-country-cont">
                            <label className="airline-country-label">Country:&nbsp;</label>
                            <label className="airline-country">{this.getCountryName(this.state.airline.CountryCode)}</label>
                        </div>

                        {(this.state.actionState == 'deleting' || this.state.actionState == 'aproving') &&
                            <img src={loadingIcon} alt="" className="loading-update-user"/>
                        }
                        <div id="pending-buttons-cont">
                            <Button id="aprove" onClick={this.showAproveAlert} text="Aprove" icon={aproveIcon}/>
                            <Button id="remove-airline" onClick={this.showRemoveAlert} text="Remove" icon={removeIcon}/>
                        </div>

                    </div>
                }


                {/* An alert before performing an action: */}
                <SweetAlert 
                    show={this.state.actionState == 'deleteAlert' || this.state.actionState == 'aproveAlert'}
                    showCancel
                    showConfirm
                    allowEscape
                    title="Confirm operation"
                    onConfirm={()=>{
                        if (this.state.actionState == 'deleteAlert')
                            this.handleRemoveAirline();
                        else
                            this.handleAproveAirline();
                    }}
                    onCancel={()=>{this.setState({actionState: 'none'})}}
                    >
                    {this.state.alertMessage}
                </SweetAlert>

                {this.state.actionState == 'error' &&
                    <label className="airline-action-error">An error occurred while processing your request.</label>
                }
                
            </div>
        )
    }
}

export default PendingAirline;