import React, {Component} from 'react';
import {Switch, Route, NavLink, Redirect} from 'react-router-dom';
import {UserContext} from '../UserContext';
import ViewService from '../services/ViewService';
import ManageFlights from './Airline/ManageFlights';
import AirlineDetails from './Airline/AirlineDetails';
import CreateFlight from './Airline/CreateFlight';
import '../styles/Airline.css';

// This is the airline company area page.
// It contains the manage-flights, the create-flight and the airline-details components.
class Airline extends Component {

    state = {
        mobileNavSelect: 'manage-flights'
    }

    handleChange = (e) => {
        this.setState({
            mobileNavSelect: e.target.value
        })
    }

    render() {
        const navActiveStyle = {
            fontWeight: 500,
            backgroundColor: 'rgb(255, 255, 255, 0.4)'
        }
        return (
            <div className="user-container">
                <div className="user-heading">Your Personal Area</div>

                    {!ViewService.isMobileView() && // Dekstop view
                        <div>
                            {/* A side navigation bar: */}
                            <div className="nav-panel">
                                <NavLink className="nav-panel-option" activeStyle={navActiveStyle} to="/user-page/manage-flights">My Flights</NavLink>
                                <NavLink className="nav-panel-option" activeStyle={navActiveStyle} to="/user-page/create-flight">Add a Flight</NavLink>
                                <NavLink className="nav-panel-option" activeStyle={navActiveStyle} to="/user-page/airline-details">Company Details</NavLink>
                            </div>
                            {/* The child components will be rendered here: */}
                            <div className="user-view">
                                    <Switch>
                                        <Route path='/user-page/manage-flights' component={ManageFlights}/>
                                        <Route path='/user-page/create-flight' component={CreateFlight}/>
                                        <Route path='/user-page/airline-details' component={AirlineDetails}/>
                                    </Switch>
                                {/* Default page: */}
                                { this.props.path == '/user-page' && 
                                    <Redirect to='/user-page/manage-flights'/>
                                }
                            </div>
                        </div>
                    }

                    {ViewService.isMobileView() && // Mobile view
                        <div>
                            {/* A side navigation bar: */}
                            <select id="airline-nav-select" className="nav-select form-control" onChange={this.handleChange} value={this.state.mobileNavSelect}>
                                <option key="0" value="manage-flights">My Flights</option>
                                <option key="1" value="create-flight">Add a Flight</option>
                                <option key="2" value="airline-details">Company Details</option>
                            </select>

                            {this.state.mobileNavSelect == 'manage-flights' &&
                                <Redirect to='/user-page/manage-flights'/>
                            }

                            {this.state.mobileNavSelect == 'create-flight' &&
                                <Redirect to='/user-page/create-flight'/>
                            }

                            {this.state.mobileNavSelect == 'airline-details' &&
                                <Redirect to='/user-page/airline-details'/>
                            }

                            {/* The child components will be rendered here: */}
                            <div className="user-view-mobile">
                                    <Switch>
                                        <Route path='/user-page/manage-flights' component={ManageFlights}/>
                                        <Route path='/user-page/create-flight' component={CreateFlight}/>
                                        <Route path='/user-page/airline-details' component={AirlineDetails}/>
                                    </Switch>
                                {/* Default page: */}
                                { this.props.path == '/user-page' && 
                                    <Redirect to='/user-page/manage-flights'/>
                                }
                            </div>
                        </div>
                    }

            </div>
        )
    }
}

Airline.contextType = UserContext;

export default Airline;