
// This service provides function that helps display date and time information in the UI.
class DateTimeService {

    // Extracts the hour an minutes from a given date and returns it in the format of hh:mm.
    getTimeString = (date) => {
        let hours = date.getHours();
        let minutes = date.getMinutes();
        if (minutes < 10)
            minutes = "0" + minutes;
        let time = `${hours}:${minutes}`;
        return time;
    }

    // Calculates and returns the duration of a flight, using the departure and landing time.
    getFlightDuration = (departureDate, landingDate) => {
        let flightTime = new Date(landingDate) - new Date(departureDate);
        let ftHoursFloat = flightTime/3600/1000;
        let ftHours = Math.floor(ftHoursFloat);
        let ftMinutes = (ftHoursFloat - ftHours)*60;
        if (ftMinutes < 10)
            ftMinutes = "0" + ftMinutes;
        return `${ftHours}:${ftMinutes}`;
    }

    // Builds a string of the date and time in a format of 'dd/MM/yyyy hh:mm'.
    getFormattedDateTimeString = (date) => {
        date = new Date(date);
        let year = date.getFullYear();
        let month = (date.getMonth() + 1) < 10 ? '0' + (date.getMonth() + 1) : (date.getMonth() + 1);
        let day = date.getDate() < 10 ? '0' + date.getDate() : date.getDate();
        let hour = date.getHours();
        let minutes = date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes();

        return `${day}/${month}/${year} ${hour}:${minutes}`;
    }

}

export default new DateTimeService();