import React, {Component} from 'react';
import {Switch, Route, NavLink, Redirect} from 'react-router-dom';
import SweetAlert from 'react-bootstrap-sweetalert';
import {UserContext} from '../UserContext';
import ViewService from '../services/ViewService';
import ManageTickets from './Customer/ManageTickets';
import CustomerDetails from './Customer/CustomerDetails';
import '../styles/Customer.css';

// This is the customer area page.
// It contains the manage-tickets and the customer-details components.
class Customer extends Component {

    state = {
        savedTicketState: 'none',
        savedTicketParams: '',
        mobileNavSelect: 'manage-tickets'
    }

    // Checks if the browser holds a saved ticket.
    // If a ticket was found, it shows an alert.
    checkForSavedTicket = () => {
        if (localStorage.hasOwnProperty('ticket'))
        {
            this.setState({
                savedTicketParams: this.getTicketParams(),
                savedTicketState: 'alert'
            })
            localStorage.removeItem('ticket')
        }
    }

    // If a saved ticket was found, it gets its information and builds query parameters string
    getTicketParams = () => {
        let ticket = JSON.parse(localStorage.getItem('ticket'));
        let params = '/purchase-ticket/params?';
        params += 'flight-type=' + ticket.ticketType;
        params += '&departure-flight-id=' + ticket.departureFlight;
        if (ticket.ticketType == 'roundtrip')
        {
            params += '&return-flight-id=' + ticket.returnFlight;
        }
        return params;
    }

    handleChange = (e) => {
        this.setState({
            mobileNavSelect: e.target.value
        })
    }

    componentDidMount() {
        this.checkForSavedTicket();
    }

    render() {
        const navActiveStyle = {
            fontWeight: 500,
            backgroundColor: 'rgb(255, 255, 255, 0.4)'
        }
        return (
            <div className="user-container">
                <div className="user-heading">Your Personal Area</div>

                    {!ViewService.isMobileView() && // Dekstop view
                        <div>
                            {/* A side navigation bar: */}
                            <div className="nav-panel">
                                <NavLink className="nav-panel-option" activeStyle={navActiveStyle} to="/user-page/manage-tickets">My Tickets</NavLink>
                                <NavLink className="nav-panel-option" activeStyle={navActiveStyle} to="/user-page/customer-details">Personal Details</NavLink>
                            </div>
                            {/* The child components will be rendered here: */}
                            <div className="user-view">
                                    <Switch>
                                        <Route path='/user-page/manage-tickets' component={ManageTickets}/>
                                        <Route path='/user-page/customer-details' component={CustomerDetails}/>
                                    </Switch>
                                {/* Default page: */}
                                { this.props.path == '/user-page' && 
                                    <Redirect to='/user-page/manage-tickets'/>
                                }
                            </div>
                        </div>
                    }

                    {ViewService.isMobileView() && // Mobile view
                    <div>
                        {/* A side navigation bar: */}
                        <select id="customer-nav-select" className="nav-select form-control" onChange={this.handleChange} value={this.state.mobileNavSelect}>
                            <option key="0" value="manage-tickets">My Tickets</option>
                            <option key="1" value="customer-details">Personal Details</option>
                        </select>

                        {this.state.mobileNavSelect == 'manage-tickets' &&
                            <Redirect to='/user-page/manage-tickets'/>
                        }

                        {this.state.mobileNavSelect == 'customer-details' &&
                            <Redirect to='/user-page/customer-details'/>
                        }

                        {/* The child components will be rendered here: */}
                        <div className="user-view-mobile">
                                <Switch>
                                    <Route path='/user-page/manage-tickets' component={ManageTickets}/>
                                    <Route path='/user-page/customer-details' component={CustomerDetails}/>
                                </Switch>
                            {/* Default page: */}
                            { this.props.path == '/user-page' && 
                                <Redirect to='/user-page/manage-tickets'/>
                            }
                        </div>
                    </div>
                    }


                    {/* An alert when a saved ticket was found: */}
                    <SweetAlert 
                        show={this.state.savedTicketState == 'alert'}
                        showCancel
                        showConfirm
                        allowEscape
                        title="Continue your purchase"
                        onConfirm={()=>this.setState({savedTicketState: 'confirmed'})}
                        onCancel={()=>this.setState({savedTicketState: 'none'})}
                        >
                        Would you like to continue to buy the ticket you selected?
                    </SweetAlert>
                    {this.state.savedTicketState == 'confirmed' &&
                        <Redirect to={this.state.savedTicketParams}/>
                    }
            </div>
        )
    }
}

Customer.contextType = UserContext;

export default Customer;