import React, {Component} from 'react';
import axios from 'axios';
import SweetAlert from 'react-bootstrap-sweetalert';
import AuthService from '../../services/AuthService';
import ErrorService from '../../services/ErrorService';
import CountriesService from '../../services/CountriesService';
import DateTimeService from '../../services/DateTimeService';
import Button from '../../components/Button';
import { updateFlightURL } from '../../Constants';
import editIcon from '../../assets/edit-icon.png';
import cancelIcon from '../../assets/cancel-icon.png';
import saveIcon from '../../assets/save-icon.png';
import deleteIcon from '../../assets/delete-icon.png';
import planeArrow from '../../assets/plane-arrow.png';
import loadingIcon from '../../assets/loading-data.gif';

// This is a child component of the manage-flights component.
// It renders all the information about the fligh and allows the user to update or cancel it.
class FlightContainer extends Component {

    // Only scheduled flights (that didn't departure yet) can be updated or deleted.

    state = {
        flight: this.props.flight, 
        newFlight: this.props.flight,
        deleteState: 'none',
        editMode: false,
        editState: 'none',
        countries: [],
        countriesState: 'loading',
        errorMessage: ''
    }
    
    // When the user confirms to the delete the flight.
    handleDeleteFlight = () => {
        this.setState({ deleteState: 'deleting' });
        this.props.deleteFunc(this.state.flight.Id, this.handleDeleteError);
    }

    handleDeleteError = () => {
        this.setState({deleteState: 'error'});
    }

    // Gets the countries from the service, so it could be used in the dropdowns.
    getCountries = async () => {
        
        let countries = await CountriesService.getCountries();
        if (countries != null)
        {
            this.setState({
                countries: countries,
                countriesState: 'loaded'
            });
        }
        else
        {
            this.setState({ countriesState: 'error'})
        }
    }

    // Gets the country's name by its Id.
    getCountryName = (id) => {
        let country = this.state.countries.find(country => {
            return country.Id == id;
        })

        return country.Name;
    }

    // Gets the flight's status.
    getFlightStatus = (depDate, landDate) => {
        if (depDate > new Date())
        {
            return 'scheduled';
        }
        if (depDate <= new Date() && landDate > new Date())
        {
            return 'departured';
        }
        if (landDate <= new Date())
        {
            return 'landed';
        }
    }

    // Handles the changes of the fields in the update form.
    handleChange = (e) => {
        let flightClone = Object.assign({}, this.state.newFlight);
        flightClone[e.target.name] = e.target.value;
        this.setState({
            newFlight: flightClone
        })
    }

    // Updates the flight's information in the server.
    updateFlight = () => {
        if (!this.validateFlightDetails(this.state.newFlight))
        {
            return;
        }      
        this.setState({ 
            editState: 'updating',
        })
        axios({
            method: 'PUT',
            url: updateFlightURL, 
            headers: AuthService.getAuthHeader().headers,
            data: this.state.newFlight
        }).then(() => {
            
            this.setState({ 
                editState: 'none',
                editMode: false,
                flight: this.state.newFlight
            })
        }).catch((error) => {
            ErrorService.logErrorToConsole(error);
            this.setState({ 
                editState: 'error',
            })
        })
    }

    // Checks that all the fields of the flight details are valid.
    validateFlightDetails = (flight) => {

        if (flight.OriginCountryCode == flight.DestinationCountryCode)
        {
            this.setState({ errorMessage: 'Origin country and destination country cannot be the same'})
            return false;
        }

        const departureTime = new Date(flight.DepartureTime);
        const landingTime = new Date(flight.LandingTime);
        if (landingTime < departureTime)
        {
            this.setState({ errorMessage: 'Landing time must be after departure time.'})
            return false;
        }
            
        if (flight.RemainingTickets == '')
        {
            this.setState({ errorMessage: 'All fields must be filled.'})
            return false;
        }

        const tickets = parseInt(flight.RemainingTickets);
        if (tickets < 0 || tickets > 1000)
        {
            this.setState({ errorMessage: 'Tickets number must be between 0 and 1000.'})
            return false;
        }

        if (flight.TicketPrice == '')
        {
            this.setState({ errorMessage: 'All fields must be filled.'})
            return false;
        }

        const ticketPrice = parseInt(flight.TicketPrice);
        if (ticketPrice < 0 || ticketPrice > 50000)
        {
            this.setState({ errorMessage: 'Ticket price must be between 0 and 50,000.'})
            return false;
        }

        return true;
    }

    // Toggles off the edit mode.
    cancelEdit = () => {
        this.setState({ editMode: false });
    }

    componentDidMount() {
        this.getCountries();
    }

    render() {

        const flight = this.state.flight;

        if (this.state.deleteState == 'deleting')
        {
            return (
                <div className="flight-cont">
                    <img src={loadingIcon} alt="" className="loading-delete-element"/>
                </div>
            )
        }
        
        const departureDateTime = new Date(flight.DepartureTime);
        const landingDateTime = new Date(flight.LandingTime);

        let flightStatus = this.getFlightStatus(departureDateTime, landingDateTime)

        const departureTime = DateTimeService.getTimeString(departureDateTime);
        const landingTime = DateTimeService.getTimeString(new Date(flight.LandingTime));
        const departureDate = departureDateTime.getDate() + '/' + (departureDateTime.getMonth() + 1) + '/' + departureDateTime.getFullYear();

        const flightTime = DateTimeService.getFlightDuration(departureDateTime, landingDateTime);

        if (this.state.countriesState == 'loading')
        {
            return null;
        }

        if (this.state.countriesState == 'error')
        {
            return (
                <div className="flight-cont">
                    <label id="flight-error">An error occurred while displaying this flight's data.</label>
                </div>
            )
        }

        const countriesOptions = this.state.countries.map(country => {
            return (
                <option key={country.Id} value={country.Id}>{country.Name}</option> 
            )
        });

        return (
            
            <div className="flight-cont" id="flight-cont-narrow">
                {/* Display mode */}
                {this.state.editMode == false &&
                    <div>
                        <div className="flight-departure">
                            <label className="flight-departure-label">Departure Date</label>
                            <label className="flight-departure-date">{departureDate}</label>
                        </div>

                        <div className="flight-start">
                            <label className="origin-country">{this.getCountryName(flight.OriginCountryCode)}</label>
                            <label className="departure-date">{departureTime}</label>
                        </div>

                        <div className="flight-span">
                            <img src={planeArrow} alt="--->" className="plane-arrow"/>
                            <label className="flight-time">{flightTime}h</label>
                        </div>

                        <div className="flight-end">
                            <label className="destination-country">{this.getCountryName(flight.DestinationCountryCode)}</label>
                            <label className="landing-date">{landingTime}</label>
                        </div>

                        <div className="flight-extra-details">
                            <div className="remain-cont">
                                <label className="remaining-tickets-label">Remaining Tickets:&nbsp;</label>
                                <label className="remaining-tickets">{flight.RemainingTickets}</label>
                            </div>
                            <div className="price-cont">
                                <label className="price-label">Ticket Price:&nbsp;</label>
                                <label className="price">{flight.TicketPrice}$</label>
                            </div>
                        </div>

                        {flightStatus == 'scheduled' && 
                            <div className="flight-buttons">
                                <Button id="edit-flight" onClick={()=>this.setState({editMode: true})} text="Edit" icon={editIcon}/>
                                <Button id="delete-flight" onClick={()=>this.setState({deleteState: 'alert'})} text="Cancel Flight" icon={deleteIcon}/>
                            </div>
                        }
                        {flightStatus == 'departured' &&
                            <label className="flight-status">Departured</label>
                        }
                        {flightStatus == 'landed' &&
                            <label className="flight-status">Landed</label>
                        }

                    </div>
                }
                {/* Edit mode */}
                {this.state.editMode == true &&
                    <div>
                        <div className="flight-departure-edit">
                            <label className="flight-departure-title">Departure Time</label>
                            <input type="datetime-local" id="flight-departure-field" name="DepartureTime" className="form-control" 
                                value={this.state.newFlight.DepartureTime} onChange={this.handleChange}></input>
                        </div>

                        <div className="flight-origin-country">
                            <label className="flight-origin-country-title">Origin Country</label>
                            <select id="flight-origin-country-field" className="form-control" onChange={this.handleChange}
                            value={this.state.newFlight.OriginCountryCode} name="OriginCountryCode">
                                {countriesOptions}
                            </select>
                        </div>

                        <div className="flight-landing">
                            <label className="flight-landing-title">Landing Time</label>
                            <input type="datetime-local" id="flight-landing-field" name="LandingTime" className="form-control" 
                                value={this.state.newFlight.LandingTime} onChange={this.handleChange}></input>
                        </div>

                        <div className="flight-destination-country">
                            <label className="flight-destination-country-title">Destination Country</label>
                            <select id="flight-destination-country-field" className="form-control" onChange={this.handleChange}
                            value={this.state.newFlight.DestinationCountryCode} name="DestinationCountryCode">
                                {countriesOptions}
                            </select>
                        </div>

                        <div className="flight-ticket-price-edit">
                            <label className="flight-ticket-price-edit-title">Ticket Price: &nbsp;</label>
                            <input type="text" id="flight-ticket-price-edit-field" name="TicketPrice" className="form-control" 
                                value={this.state.newFlight.TicketPrice} onChange={this.handleChange}></input>
                        </div>

                        <div className="flight-remaining-tickets-edit">
                            <label className="flight-remaining-tickets-title">Remaining Tickets: &nbsp;</label>
                            <input type="text" id="flight-remaining-tickets-field" name="RemainingTickets" className="form-control" 
                                value={this.state.newFlight.RemainingTickets} onChange={this.handleChange}></input>
                        </div>

                        <Button id="cancel" onClick={(this.cancelEdit)} text="Cancel" icon={cancelIcon}/>
                        <Button id="save" onClick={this.updateFlight} text="Save" icon={saveIcon}/>

                        {this.state.editState == 'updating' &&
                            <img src={loadingIcon} alt="" className="loading-update-user"/>
                        }
                    </div>
                }

                {/* An alert before removing the flight: */}
                <SweetAlert 
                    show={this.state.deleteState == 'alert'}
                    showCancel
                    showConfirm
                    allowEscape
                    title="Confirm operation"
                    onConfirm={this.handleDeleteFlight}
                    onCancel={()=>{this.setState({deleteState: 'none'})}}
                    >
                    {`Are you sure you want to cacnel this flight?`}
                </SweetAlert>

                {/* Validation error message: */}
                <SweetAlert 
                    error
                    show={this.state.errorMessage != ''}
                    showConfirm
                    allowEscape
                    title={this.state.errorMessage}
                    onConfirm={()=>this.setState({errorMessage: ''})}
                    >
                </SweetAlert>

                {/* Update error message: */}
                <SweetAlert 
                    error
                    show={this.state.editState == 'error'}
                    showConfirm
                    allowEscape
                    title="An error occurred while updating the flight."
                    onConfirm={()=>this.setState({editState: 'none'})}
                    >
                </SweetAlert>

                {this.state.deleteState == 'error' &&
                    <label id="delete-flight-error">An error occurred while canceling the flight.</label>
                }
            </div>
        )
    }
}

export default FlightContainer;