import React, {Component} from 'react';
import axios from 'axios';
import SweetAlert from 'react-bootstrap-sweetalert';
import AuthService from '../../services/AuthService';
import ErrorService from '../../services/ErrorService';
import {UserContext} from '../../UserContext';
import TicketContainer from './TicketContainer';
import { getCustomerFlightsURL, deleteTicketURL } from '../../Constants';
import loadingData from '../../assets/loading-data.gif';
import errorIcon from '../../assets/error-icon.png';
import infoIcon from '../../assets/info-icon.png';
import '../../styles/Customer.css';

// This component is a part of the customer area.
// It displays all the customer's flight tickets and allows him to cancel them.
class ManageTickets extends Component {

    // In order to display the information about the tickets, the component gets
    // the whole flights from the server.

    // When a customer chooses to cancel a ticket, it will be deleted.

    state = {
        flights: [],
        flightsState: '',
        ticketDeleted: false
    }

    CancelToken = axios.CancelToken;
    source = this.CancelToken.source();

    // Gets all the flights which this customer owns a ticket to.
    getCustomerFlights = () => {
        
        this.setState({ 
            flightsState: 'loading'
        }) 

        axios({
            url: getCustomerFlightsURL, 
            headers: AuthService.getAuthHeader().headers,
        }).then((response) => {        
            this.setState({ 
                flights: response.data,
                flightsState: 'loaded'
            })
        }).catch((error) => {
            ErrorService.logErrorToConsole(error);
            this.setState({ 
                flightsState: 'error'
            })
        })
        
    }

    // Deletes the selected ticket.
    deleteTicket = (ticketId, handleError) => {
        axios({
            method: 'DELETE',
            url: deleteTicketURL + '/' + ticketId, 
            headers: AuthService.getAuthHeader().headers,
        }).then(() => {
            this.removeTicketFromList(ticketId);
            this.setState({ticketDeleted: true});
        }).catch((error) => {
            ErrorService.logErrorToConsole(error);
            handleError();
        })
    }

    // Removes the deleted ticket from the view.
    removeTicketFromList = (ticketId) => {
        let newTicketsList = [];
        this.state.flights.forEach(flight => {
            if (flight.TicketId != ticketId)
                newTicketsList.push(flight);
        });
        this.setState({flights: newTicketsList})
    }

    componentDidMount() {
        this.getCustomerFlights();
    }

    componentWillUnmount() {
        this.source.cancel();
    }

    render() {
        if (this.state.flightsState == 'loading')
        {
            return (
                <div id="manage-tickets-container">
                    <img src={loadingData} alt="" className="loading-user-view"/>
                </div>
            )
        }
        if (this.state.flightsState == 'error')
        {
            return (
                <div className="error-container">
                    <img src={errorIcon} alt="error" className="error-icon"/>
                    <h4 className="error-message">An error occurred while receiving the data.</h4>
                </div>
            )
        }
        // Mapping the flights to ticket container components.
        const tickets = this.state.flights.map(flight => {

            return (
                <TicketContainer flight={flight} key={flight.Id} deleteFunc={this.deleteTicket}/>
            )
        });

        let instructions = 'Here you can see and manage all your flight tickets.'

        const info = 'Only tickets for future flights will be displayed here.'

        if (tickets.length == 0)
        {
            instructions = "No tickets to show."
        }

        return (
            <div id="manage-tickets-container">

                <p className="instructions">
                    {instructions}&nbsp;
                    <img src={infoIcon} title={info} alt="" className="info-icon"/>
                </p>
                
                <div className="tickets-cont">
                    {tickets}
                </div>
                {/* A notice that the ticket was deleted: */}
                    <SweetAlert
                    success 
                    show={this.state.ticketDeleted}
                    showConfirm
                    allowEscape
                    title="The ticket was canceled"
                    onConfirm={()=>{this.setState({ticketDeleted: false})}}
                    >
                    {`You will get a refund to your credit card in 24 hours.`}
                </SweetAlert>
            </div>
        )
    }
}

ManageTickets.contextType = UserContext;

export default ManageTickets;