import React, {Component} from 'react';
import SweetAlert from 'react-bootstrap-sweetalert';
import DateTimeService from '../../services/DateTimeService';
import Button from '../../components/Button';
import cancelIcon from '../../assets/cancel-icon.png';
import loadingIcon from '../../assets/loading-data.gif';
import planeArrow from '../../assets/plane-arrow.png';

// This component contains all the information about the flight ticket.
// It also allows the user to remove (cancel) it.
class TicketContainer extends Component {

    state = {
        flight: this.props.flight,
        deleteState: 'none'
    }

    // Present an alert for the user to confirm the delete operation.
    showDeleteAlert = () => {
        this.setState({deleteState: 'deleteAlert'})
    }

    // When the user confirms to delete, this funcion will invoke the deletion function that
    // was recieved from the parent component, and then the parent will delete the ticket.
    handleDeleteAirline = () => {
        this.setState({ 
            deleteState: 'deleting' 
        });
        this.props.deleteFunc(this.state.flight.TicketId, this.deleteErrorHandle);
    }

    // When an error occurrs while deleting the airline.
    deleteErrorHandle = () => {
        this.setState({deleteState: 'error'})
    }

    render() {

        const flight = this.state.flight;

        if (this.state.deleteState == 'deleting')
        {
            return (
                <div className="flight-cont">
                    <img src={loadingIcon} alt="" className="loading-delete-element"/>
                </div>
            )
        }
        
        const departureDateTime = new Date(flight.DepartureTime);
        const landingDateTime = new Date(flight.LandingTime)

        const departureTime = DateTimeService.getTimeString(departureDateTime);
        const landingTime = DateTimeService.getTimeString(new Date(flight.LandingTime));
        const departureDate = departureDateTime.getDate() + '/' + (departureDateTime.getMonth() + 1) + '/' + departureDateTime.getFullYear();

        const flightTime = DateTimeService.getFlightDuration(departureDateTime, landingDateTime);

        return (
            
            <div className="flight-cont" id="flight-cont-narrow">

                <div className="flight-departure">
                    <label className="flight-departure-title">Departure Date</label>
                    <label className="flight-departure-date">{departureDate}</label>
                </div>

                <label className="flight-airline">{flight.AirlineCompany}</label>

                <div className="flight-start">
                    <label className="origin-country">{flight.OriginCountry}</label>
                    <label className="departure-date">{departureTime}</label>
                </div>

                <div className="flight-span">
                    <img src={planeArrow} alt="--->" className="plane-arrow"/>
                    <label className="flight-time">{flightTime}h</label>
                </div>

                <div className="flight-end">
                    <label className="destination-country">{flight.DestinationCountry}</label>
                    <label className="landing-date">{landingTime}</label>
                </div>

                <Button id="delete-ticket" onClick={this.showDeleteAlert} text="Cancel Ticket" icon={cancelIcon}/>
                 
                {/* An alert before removing the ticket: */}
                <SweetAlert 
                    show={this.state.deleteState == 'deleteAlert'}
                    showCancel
                    showConfirm
                    allowEscape
                    title="Confirm operation"
                    onConfirm={this.handleDeleteAirline}
                    onCancel={()=>{this.setState({deleteState: 'none'})}}
                    >
                    {`Are you sure you want to cacnel this ticket?`}
                </SweetAlert>

                {this.state.deleteState == 'error' &&
                    <label id="ticket-delete-error">An error occurred while removing the ticket.</label>
                }
            </div>
        )
        
    }
    
}

export default TicketContainer;