import React from 'react';
import { DealsPageURL } from '../Constants';

// This component contains the deals page.
// NOTICE: The deals page is an external page. It is being presented here inside an iframe element.
const DealsPage = function(props) {

    return (
        <div id="deals-page-container" className="page-content-container">
        <iframe id="deals-frame" src={DealsPageURL} className="iframe-cont" title="deals-page"></iframe>
        </div>
    )
}

export default DealsPage;