import React, {Component} from 'react';
import DateTimeService from '../../services/DateTimeService';
import loadingPlane from '../../assets/loading-plane.gif';
import errorIcon from '../../assets/error-icon.png';
import planeArrow from '../../assets/plane-arrow.png';

// This component holds and renders the results of the flights search.
class SearchResults extends Component {

    state = {
        results: []
    }

    // Parses the time data of a given flight.
    getFlightData = (flight) => {

        let departureTime = DateTimeService.getTimeString(new Date(flight.departureDate));
        let landingTime = DateTimeService.getTimeString(new Date(flight.landingDate));
        let flightTime = DateTimeService.getFlightDuration(flight.departureDate, flight.landingDate);

        return {
            departureTime: departureTime,
            landingTime: landingTime,
            flightDuration: flightTime
        }
    }

    render() {

        if (this.props.submitState == "notSubmitted")
        {
            return null;
        }

        if (this.props.submitState == "loading")
        {
            return (
                <img src={loadingPlane} alt="loading..." id="loading-flights-results"/>
            )
        }

        if (this.props.submitState == "error")
        {
            return (
                <div className="error-container" id="flights-results-error">
                    <img src={errorIcon} alt="error" className="error-icon"/>
                    <h4 className="error-message">An error occurred while receiving the data. </h4>
                </div>
            )
        }

        if (this.props.submitState == "submitted" && this.props.flights.length == 0)
        {
            return <p id="no-flights-message">Sorry. No flights to were found.</p>
        }

        if (this.props.roundtrip)
        {
            // Checking if the flights array is in the right form (pairs of flights).
            if (!this.props.flights[0].hasOwnProperty('flightId'))
            {
                this.state.results = this.props.flights.map(flightsPair => {

                    const f1 = this.getFlightData(flightsPair[0]);
                    const f2 = this.getFlightData(flightsPair[1]);
                    const ticketPrice = parseInt(flightsPair[0].ticketPrice) + parseInt(flightsPair[1].ticketPrice) + 1;
    
                    return (
                        <div className="flights-pair" key={flightsPair[0].flightId} 
                            onClick={()=>this.props.buyTicket(flightsPair[0].flightId, flightsPair[1].flightId)}>
                            
                            <div className="flights-pair-data">
                                <div className="departure-flight-data">
                                    <label className="airline-name">{flightsPair[0].airlineName}</label>
    
                                    <div className="flight-start">
                                        <label className="origin-country">{flightsPair[0].originCountry}</label>
                                        <label className="departure-date">{f1.departureTime}</label>
                                    </div>
                                    <div className="flight-span">
                                        <img src={planeArrow} alt="--->" className="plane-arrow"/>
                                        <label className="flight-time">{f1.flightDuration}h</label>
                                    </div>
                                    <div className="flight-end">
                                        <label className="destination-country">{flightsPair[0].destinationCountry}</label>
                                        <label className="landing-date">{f1.landingTime}</label>
                                    </div>
                                </div>
    
                                <div className="return-flight-data">
                                    <label className="airline-name">{flightsPair[1].airlineName}</label>
    
                                    <div className="flight-start">
                                        <label className="origin-country">{flightsPair[1].originCountry}</label>
                                        <label className="departure-date">{f2.departureTime}</label>
                                    </div>
                                    <div className="flight-span">
                                        <img src={planeArrow} alt="--->" className="plane-arrow"/>
                                        <label className="flight-time">{f2.flightDuration}h</label>
                                    </div>
                                    <div className="flight-end">
                                        <label className="destination-country">{flightsPair[1].destinationCountry}</label>
                                        <label className="landing-date">{f2.landingTime}</label>
                                    </div>
                                </div>
                            </div>
                            <div className="offer-pair">
                                    <label className="ticket-price">{ticketPrice}$</label>
                                    <label className="book-now">Book Now!</label>
                            </div>
                        </div>
                    )
                })
            }
        }  
        
        if (this.props.roundtrip == false)
        {
            // Checking if the flights array is in the right form (single flights).
            if (this.props.flights[0].hasOwnProperty('flightId'))
            {
                this.state.results = this.props.flights.map(flight => {

                    const f = this.getFlightData(flight);
    
                    return (
                        <div className="flight" key={flight.flightId} onClick={()=>this.props.buyTicket(flight.flightId)}>
    
                            <label className="airline-name">{flight.airlineName}</label>
    
                            <div className="flight-start">
                                <label className="origin-country">{flight.originCountry}</label>
                                <label className="departure-date">{f.departureTime}</label>
                            </div>
                            <div className="flight-span">
                                <img src={planeArrow} alt="--->" className="plane-arrow"/>
                                <label className="flight-time">{f.flightDuration}h</label>
                            </div>
                            <div className="flight-end">
                                <label className="destination-country">{flight.destinationCountry}</label>
                                <label className="landing-date">{f.landingTime}</label>
                            </div>
                            <div className="offer">
                                <label className="ticket-price">{flight.ticketPrice}$</label>
                                <label className="book-now">Book Now!</label>
                            </div>
    
                        </div>
                    )
                })
            }
        }

        return (
            <div id="flights-results">
                <h5>Results:</h5>
                {this.state.results}
            </div>
        )
    }
}

export default SearchResults;