import React, {Component} from 'react';
import axios from 'axios';
import AuthService from '../../services/AuthService';
import ErrorService from '../../services/ErrorService';
import AirlineContainer from './AirlineContainer';
import { getAllAirlinesURL, deleteAirlineURL } from '../../Constants';
import loadingData from '../../assets/loading-data.gif';
import errorIcon from '../../assets/error-icon.png';

// This component is a part of the admin area.
// It displays all the registered airline companies in the database, and allows the user to
// update or remove them.
class ManageAirlines extends Component {

    state = {
        dataState: 'loading',
        airlines: [],
        'name-filter': '',
        'id-filter': '',
    }

    CancelToken = axios.CancelToken;
    source = this.CancelToken.source();

    // Getting all airline companies from the server.
    getAirlines = () => {
        axios({
            url: getAllAirlinesURL, 
            cancelToken: this.source.token
        }).then((response) => {
            this.setState({ 
                dataState: 'loaded',
                airlines: response.data
            })
        }).catch((error) => {
            if (axios.isCancel(error))
            {
                console.log('The AJAX request was canceled.');
            }
            else
            {
                ErrorService.logErrorToConsole(error);
                this.setState({ 
                    dataState: 'error'
                })
            }
        })
    }

    // Deletes the airline from the server.
    deleteAirline = (airline, handleError) => {
        axios({
            method: 'DELETE',
            url: deleteAirlineURL, 
            headers: AuthService.getAuthHeader().headers,
            data: airline
        }).then(() => {
            this.removeAirlineFromList(airline.Id);
        }).catch((error) => {
            ErrorService.logErrorToConsole(error);
            handleError();
        })
    }

    // Removes the airline from the list (after it was deleted).
    removeAirlineFromList = (airlineId) => {
        let newAirlinesList = [];
        this.state.airlines.forEach(airline => {
            if (airline.Id != airlineId)
                newAirlinesList.push(airline);
        });
        this.setState({airlines: newAirlinesList})
    }

    // Handles the changes in the filter fields.
    handleChange = (e) => {
        this.setState({
            [e.target.id]: e.target.value
        })
    }

    // Checks if a given airline is filtered out by its Id or name.
    isFilteredOut = (airline) => {
        if (this.state["name-filter"] == '' && this.state["id-filter"] == '')
        {
            return false;
        }
        else if (this.state["name-filter"] != '')
        {
            if (!airline.Name.toUpperCase().includes(this.state['name-filter'].toUpperCase()))
            {
                return true;
            }
        }
        else if (this.state["id-filter"] != '')
        {
            if (airline.Id != this.state['id-filter'])
            {
                return true;
            }
        }
        else
        {
            return false;
        }
    }

    initializeData = async () => {
        this.getAirlines();
    }

    componentDidMount() {
        this.initializeData();
    }

    componentWillUnmount() {
        this.source.cancel();
    }

    render() {   

        if (this.state.dataState == 'loading')
        {
            return (
                <img src={loadingData} alt="" className="loading-user-view"/>
            )
        }
        if (this.state.dataState == 'error')
        {
            return (
                <div className="error-container">
                    <img src={errorIcon} alt="" className="error-icon"/>
                    <h4 className="error-message">An error occurred while receiving the data. </h4>
                </div>
            )
        }

        let colorKey = -1; // This is being used to give different color to each airline's icon.

        // Mapping the airlines to AirlineContainer components.
        const displayedAirlines = this.state.airlines.map(airline => {

            if (this.isFilteredOut(airline))
            {
                return null;
            }

            colorKey++;

            return (
                <AirlineContainer airline={airline} key={airline.Id} colorKey={colorKey} deleteFunc={this.deleteAirline}/>
            )
        })

        return (
            <div id="airlines-container">
                <p id="manage-airlines-instructions" className="instructions">Here you can manage all registered airlines accounts.</p>
                <div id="airlines-filter-form">

                    <div className="filter-field-cont">
                        <label id="name-filter-label" htmlFor="name-filter">Filter by name: </label>
                        <input type='text' id="name-filter" onChange={this.handleChange}></input>
                    </div>

                    <div className="filter-field-cont">
                        <label id="id-filter-label" htmlFor="id-filter">Filter by Id: </label>
                        <input type='text' id="id-filter" onChange={this.handleChange}></input>
                    </div>

                    { displayedAirlines.length == this.state.airlines.length && 
                        <label id="total-airlines">Displaying {this.state.airlines.length} airlines:</label>
                    }
                </div>
                <div id="airlines-list" className="list-view">
                    {displayedAirlines}
                </div>
                {this.state.airlines.length == 0 &&
                    <label>No airlines to show.</label>
                }
            </div>
        )
    }
}

export default ManageAirlines;