import React, {Component} from 'react';
import axios from 'axios';
import AuthService from '../../services/AuthService';
import ErrorService from '../../services/ErrorService';
import CustomerContainer from './CustomerContainer';
import { getAllCustomersURL, deleteCustomerURL } from '../../Constants';
import loadingData from '../../assets/loading-data.gif';
import errorIcon from '../../assets/error-icon.png';

// This component is a part of the admin area.
// It displays all the registered customers in the database and allows to update or delete them.
class ManageCustomers extends Component {

    state = {
        customersState: 'loading',
        customers: [],
        'name-filter': '',
        'id-filter': ''
    }

    CancelToken = axios.CancelToken;
    source = this.CancelToken.source();

    getCustomers = () => {
        axios({
            url: getAllCustomersURL, 
            headers: AuthService.getAuthHeader().headers, 
            cancelToken: this.source.token
        }).then(      
            response => {
                return response.data;
            }
        ).then((data) => {
            this.setState({ 
                customersState: 'loaded',
                customers: data
            })
        }).catch((error) => {
            if (axios.isCancel(error))
            {
                console.log('The AJAX request was canceled.');
            }
            else
            {
                ErrorService.logErrorToConsole(error);
                this.setState({ 
                    customersState: 'error'
                })
            }
        })
    }

    // Sends a request to delete the customer from the server.
    // This function is called by the child component "CustomerContainer"
    deleteCustomer = (customer, handleError) => {
        axios({
            method: 'DELETE',
            url: deleteCustomerURL, 
            headers: AuthService.getAuthHeader().headers,
            data: customer
        }).then(() => {
            this.removeCustomerFromList(customer.Id);
        }).catch((error) => {
            ErrorService.logErrorToConsole(error);
            handleError();
        })
    }

    // Removes the customer from the view.
    removeCustomerFromList = (customerId) => {
        let newCustomersList = [];
        this.state.customers.forEach(customer => {
            if (customer.Id != customerId)
                newCustomersList.push(customer);
        });
        this.setState({customers: newCustomersList})
    }

    handleChange = (e) => {
        this.setState({
            [e.target.id]: e.target.value
        })
    }

    // Checks if a given customer is filtered out by its Id or name.
    isFilteredOut = (customerId, customerFullName) => {
        if (this.state["name-filter"] == '' && this.state["id-filter"] == '')
        {
            return false;
        }
        else if (this.state["name-filter"] != '')
        {
            if (!customerFullName.toUpperCase().includes(this.state['name-filter'].toUpperCase()))
            {
                return true;
            }
        }
        else if (this.state["id-filter"] != '')
        {
            if (customerId != this.state['id-filter'])
            {
                return true;
            }
        }
        else
        {
            return false;
        }
    }

    componentDidMount() {
        this.getCustomers();
    }

    componentWillUnmount() {
        this.source.cancel();
    }

    render() {   

        if (this.state.customersState == 'loading')
        {
            return (
                <img src={loadingData} alt="" className="loading-user-view"/>
            )
        }
        if (this.state.customersState == 'error')
        {
            return (
                <div className="error-container">
                    <img src={errorIcon} alt="error" className="error-icon"/>
                    <h4 className="error-message">An error occurred while receiving the data.</h4>
                </div>
            )
        }

        let colorKey = -1; // This is being used to give different color to each customer icon.

        const displayedCustomers = this.state.customers.map(customer => {

            let customerFullName = `${customer.FirstName} ${customer.LastName}`

            if (this.isFilteredOut(customer.Id, customerFullName))
            {
                return null;
            }

            colorKey++;

            return (
                <CustomerContainer customer={customer} key={customer.Id} colorKey={colorKey} deleteFunc={this.deleteCustomer}/>
            )
        })

        return (
            <div id="customers-container">
                <p id="manage-customer-instructions" className="instructions">Here you can manage all registered customer accounts.</p>
                <div id="customers-filter-form">

                    <div className="filter-field-cont">
                        <label id="name-filter-label" htmlFor="name-filter">Filter by name: </label>
                        <input type='text' id="name-filter" onChange={this.handleChange}></input>
                    </div>

                    <div className="filter-field-cont">
                        <label id="id-filter-label" htmlFor="id-filter">Filter by Id: </label>
                        <input type='text' id="id-filter" onChange={this.handleChange}></input>
                    </div>

                    { displayedCustomers.length == this.state.customers.length && 
                        <label id="total-customers">Displaying {this.state.customers.length} customers:</label>
                    }
                </div>
                <div id="customers-list" className="list-view">
                    {displayedCustomers}
                </div>
                {this.state.customers.length == 0 &&
                    <label>No customers to show.</label>
                }
            </div>
        )
    }
}

export default ManageCustomers;