import React, {Component} from 'react';
import axios from 'axios';
import SweetAlert from 'react-bootstrap-sweetalert';
import AuthService from '../../services/AuthService';
import ErrorService from '../../services/ErrorService';
import {UserContext} from '../../UserContext';
import Button from '../Button';
import HiddenField from '../HiddenField';
import PasswordChange from '../PasswordChange';
import { modifyCustomerDetailsURL } from '../../Constants';
import editIcon from '../../assets/edit-icon.png';
import saveIcon from '../../assets/save-icon.png';
import cancelIcon from '../../assets/cancel-icon.png';
import loadingIcon from '../../assets/loading-data.gif';
import '../../styles/Customer.css';

// This component is a part of the customer area.
// It displays all the customer's details and allows him to change them.
class CustomerDetails extends Component {
    
    state = {
        customer: this.context.userInfo.user,
        newCustomer: this.context.userInfo.user,
        editMode: false,
        validationAlert: false,
        updateState: 'none'
    }

    // When the user clicks the 'Edit' button:
    setEditMode = () => {
        this.setState({editMode: true})
    }

    // When the user clicks the 'Cancel' button:
    cancelEditMode = () => {
        this.setState({
            editMode: false,
            newCustomer: this.state.customer
        })
    }

    // Handles the changes in the form details form.
    handleChange = (e) => {
        let customerClone = Object.assign({}, this.state.newCustomer);
        customerClone[e.target.id] = e.target.value;
        this.setState({
            newCustomer: customerClone
        })
    }

    // Validates that none of the customer's fields are empty before updating.
    validateCustomerDetails = (customer) => {
        if (customer.FirstName == '' ||
            customer.LastName == '' ||
            customer.Email == '' ||
            customer.UserName == '' ||
            customer.Password == '' ||
            customer.Address == '' ||
            customer.PhoneNo == '' )
        {
            return false;
        }
        else
            return true;
    }

    // Updating the customer with the new details.
    updateCustomerDetails = () => {
        if (!this.validateCustomerDetails(this.state.newCustomer))
        {
            this.setState({ 
                validationAlert: true,
            })
            return;
        }

        this.setState({ 
            updateState: 'loading'
        })

        axios({
            method: 'PUT',
            url: modifyCustomerDetailsURL, 
            headers: AuthService.getAuthHeader().headers,
            data: this.state.newCustomer
        }).then(() => {
            this.renewUserToken();
        }).catch((error) => {
            ErrorService.logErrorToConsole(error);
            this.setState({ 
                updateState: 'error',
            })
        })
    }

    // Renewing the token with the updated details.
    renewUserToken = () => {
        let newCustUsername = this.state.newCustomer.UserName;
        let newCustPassword = this.state.newCustomer.Password;
        if (this.context.updateUserInfo([newCustUsername, newCustPassword]))
        {
            this.setState({ 
                customer: this.state.newCustomer,
                updateState: 'updated',
                editMode: false
            })
        }
        else
        {
            this.setState({ 
                customer: this.state.newCustomer,
                updateState: 'tokenUpdateError',
                editMode: false
            })
        }
    }

    // When the customer changes its password using the PasswordChange component, it handles everything:
    handlePasswordChange = (newPassword) => {
        let customerClone = Object.assign({}, this.state.newCustomer);
        customerClone.Password = newPassword;
        this.setState({
            newCustomer: customerClone
        })
        this.renewUserToken();
    }

    render() {

        // A shorthand
        const newCustomer = this.state.newCustomer;

        return (
            <div id="customer-details-container">
                <div id="customer-details">

                    {/* When in edit mode, the details text blocks will be replaced with editable fields */}

                    {/* Left Column */}
                    <div id="cust-details-left-column" className="details-column">

                        <div id="cust-first-name" className="field-cont">
                            <label id="cust-first-name" className="label">First Name: &nbsp;</label>
                            {!this.state.editMode &&
                                <label id="cust-first-name" className="text">{newCustomer.FirstName}</label>
                            }
                            {this.state.editMode &&
                                <input type="text" id="FirstName" className="cust-first-name" defaultValue={newCustomer.FirstName} onChange={this.handleChange}/>
                            }
                        </div>
                        <div id="cust-user-name" className="field-cont">
                            <label id="cust-user-name" className="label">Username: &nbsp;</label>
                            {!this.state.editMode &&
                                <label id="cust-user-name" className="text">{newCustomer.UserName}</label>
                            }
                            {this.state.editMode &&
                                <input type="text" id="UserName" className="cust-user-name" defaultValue={newCustomer.UserName} onChange={this.handleChange}/>
                            }
                        </div>
                        <div id="cust-address" className="field-cont">
                            <label id="cust-address" className="label">Address: &nbsp;</label>
                            {!this.state.editMode &&
                                <label id="cust-address" className="text">{newCustomer.Address}</label>
                            }
                            {this.state.editMode &&
                                <input type="text" id="Address" className="cust-address" defaultValue={newCustomer.Address} onChange={this.handleChange}/>
                            }
                        </div>
                        <div id="cust-password" className="field-cont">
                            <PasswordChange userType="customer" handlePasswordChange={this.handlePasswordChange}/>
                        </div>

                    </div>
                    
                    {/* Right Column */}
                    <div id="cust-details-right-column" className="details-column">

                        <div id="cust-last-name" className="field-cont">
                            <label id="cust-last-name" className="label">Last Name: &nbsp;</label>
                            {!this.state.editMode &&
                                <label id="cust-last-name" className="text">{newCustomer.LastName}</label>
                            }
                            {this.state.editMode &&
                                <input type="text" id="LastName" className="cust-last-name" defaultValue={newCustomer.LastName} onChange={this.handleChange}/>
                            }
                        </div>
                        <div id="cust-email" className="field-cont">
                            <label id="cust-email" className="label">Email: &nbsp;</label>
                            {!this.state.editMode &&
                                <label id="cust-email" className="text">{newCustomer.Email}</label>
                            }
                            {this.state.editMode &&
                                <input type="text" id="Email" className="cust-email" defaultValue={newCustomer.Email} onChange={this.handleChange}/>
                            }
                        </div>
                        <div id="cust-phone" className="field-cont">
                            <label id="cust-phone" className="label">Phone Number: &nbsp;</label>
                            {!this.state.editMode &&
                                <label id="cust-phone" className="text">{newCustomer.PhoneNo}</label>
                            }
                            {this.state.editMode &&
                                <input type="text" id="PhoneNo" className="cust-phone" defaultValue={newCustomer.PhoneNo} onChange={this.handleChange}/>
                            }
                        </div>
                        <div id="cust-credit-card" className="field-cont">
                            
                            {!this.state.editMode &&
                                <HiddenField id="cust-credit-card" label="Credit Card Number:" value={newCustomer.CreditCardNo}/>
                            }
                            {this.state.editMode &&
                                <div style={{display: 'inline-block'}}>
                                    <label id="cust-credit-card" className="label">Credit Card Number: &nbsp;</label>
                                    <input type="text" id="CreditCardNo" className="cust-credit-card" defaultValue={newCustomer.CreditCardNo} onChange={this.handleChange}/>
                                </div>
                            }
                        </div>

                    </div>

                </div>

                <SweetAlert 
                    error
                    show={this.state.validationAlert}
                    showConfirm
                    allowEscape
                    title="All fields must be filled."
                    onConfirm={() => this.setState({validationAlert: false})}
                    >
                </SweetAlert>

                <SweetAlert 
                    error
                    show={this.state.updateState == 'error'}
                    showConfirm
                    allowEscape
                    title="An error occurred while updating the data."
                    onConfirm={() => this.setState({updateState: 'none'})}
                    >
                </SweetAlert>

                <SweetAlert 
                    error
                    show={this.state.updateState == 'tokenUpdateError'}
                    showConfirm
                    allowEscape
                    title="An error occurred while updating your login token"
                    onConfirm={() => this.setState({updateState: 'none'})}
                    >
                    We recommended you to logout and then login again.
                </SweetAlert>

                {!this.state.editMode &&
                        <Button id="edit-cust-details" text="Edit" icon={editIcon} onClick={this.setEditMode}/>
                }
                {this.state.editMode &&
                    <div className="cust-edit-buttons">
                        <Button id="cancel-cust-details" text="Cancel" icon={cancelIcon} onClick={this.cancelEditMode}/>
                        <Button id="save-cust-details" text="Save" icon={saveIcon} onClick={this.updateCustomerDetails}/>
                        {this.state.updateState == 'loading' &&
                            <img src={loadingIcon} alt="" class="loading-update-user"/>
                        }
                    </div>
                }

            </div>
        )
    }
}

CustomerDetails.contextType = UserContext;

export default CustomerDetails;