import React, {Component} from 'react';
import {Redirect} from 'react-router-dom';
import {UserContext} from '../UserContext';
import Admin from './Admin';
import Customer from './Customer';
import Airline from './Airline';
import '../styles/UserPage.css'

// This component serves as a HOC for the different user pages.
// It detects the user type and renders the fitting component.
class UserPage extends Component {

    render() {
        const user = this.context;
        if (user.userInfo.role == "Customer")
        {
            return (
                <Customer path={this.props.history.location.pathname}/>
            );
        }
        else if (user.userInfo.role == "AirlineCompany")
        {
            return (
                <Airline path={this.props.history.location.pathname}/>
            )
        }
        else if (user.userInfo.role == "Administrator")
        {
            return (
                <Admin path={this.props.history.location.pathname}/>
            )
        }
        else
        {   
            // In case the current user is anonymous and not registered,
            // He will be redirected to the home page.
            return (
                <Redirect to="/"/>
            )
        }
    }
}

UserPage.contextType = UserContext;

export default UserPage;