import axios from 'axios';
import ErrorService from './ErrorService';
import { getCountriesURL } from '../Constants';

// This service gets the countries from the server, stores it and provides it to any component
// that requests it.
class CountriesService {

    countries;

    getCountries = async () => {
        if (this.countries == null)
        {
            await this.getCountriesFromAPI();
        }
        return this.countries;
    }

    getCountriesFromAPI = async () => {
        await axios.get(getCountriesURL)
        .then((response) => {
            this.countries = response.data;
        }).catch((error) => {
            ErrorService.logErrorToConsole(error);
        })
    }
}

export default new CountriesService();