import React, { Component } from 'react';
import { NavLink } from 'react-router-dom';
import registerImage from '../assets/register.jpg';
import '../styles/Register.css';

// This is a simple referal page for new users.
// It refers to the customer and airline company registration pages.
class Register extends Component {
    render() {
        return (
            <div id="register-container" className="page-content-container">

                <h2>New to Flights Center?</h2>
                <h4>We always welcome new members.</h4>

                <img src={registerImage} alt="" id="register-image"/>

                <div id="sign-up-offers">
                    <h5>Sign up as a <NavLink to="/customer-register">Customer</NavLink>.</h5>
                    <h5>Sign up as an <NavLink to="/airline-register">Airline Company</NavLink>.</h5>
                </div>

                <h5 id="login-offer">Already a member? <NavLink to="/login">Login</NavLink></h5>
                <h5 id="contact-offer">Any questions? <NavLink to="/about">Contact Us</NavLink></h5>

            </div>
        )
    }
}

export default Register;