import React, {Component} from 'react';
import axios from 'axios';
import SweetAlert from 'react-bootstrap-sweetalert';
import AuthService from '../../services/AuthService';
import ErrorService from '../../services/ErrorService';
import CountriesService from '../../services/CountriesService';
import {UserContext} from '../../UserContext';
import Button from '../Button';
import PasswordChange from '../PasswordChange';
import { modifyAirlineDetailsURL } from '../../Constants';
import loadingData from '../../assets/loading-data.gif';
import editIcon from '../../assets/edit-icon.png';
import saveIcon from '../../assets/save-icon.png';
import cancelIcon from '../../assets/cancel-icon.png';
import loadingIcon from '../../assets/loading-data.gif';
import errorIcon from '../../assets/error-icon.png'
import '../../styles/Airline.css';

// This component is a part of the airline company area.
// It displays all the airline's details and allows it to change them.
class AirlineDetails extends Component {
    
    state = {
        airline: this.context.userInfo.user,
        newAirline: this.context.userInfo.user,
        countries: 'loading',
        editMode: false,
        validationAlert: false,
        updateState: 'none'
    }

    CancelToken = axios.CancelToken;
    source = this.CancelToken.source();

    // Gets all the countries from the dedicated service.
    getCountries = async () => {
        
        let countries = await CountriesService.getCountries();
        if (countries != null)
        {
            this.setState({
                countries: countries,
            });
        }
        else
        {
            this.setState({ countries: 'error'})
        }
    }

    // When the user clicks the 'Cancel' button:
    cancelEditMode = () => {
        this.setState({
            editMode: false,
            newCustomer: this.state.airline
        })
    }

    // When a field in the form is changed, this updates the state.
    handleChange = (e) => {
        let airlineClone = Object.assign({}, this.state.newAirline);
        airlineClone[e.target.id] = e.target.value;
        this.setState({
            newAirline: airlineClone
        })
    }

    // Validates that none of the airline's fields are empty before updating.
    validateAirlineDetails = (airline) => {
        if (airline.Name == '' ||
            airline.Email == '' ||
            airline.UserName == '' )
        {
            return false;
        }
        else
            return true;
    }

    // Updating the airline with the new details.
    updateAirlineDetails = () => {
        if (!this.validateAirlineDetails(this.state.newAirline))
        {
            this.setState({ 
                validationAlert: true,
            })
            return;
        }

        this.setState({ 
            updateState: 'loading'
        })

        axios({
            method: 'PUT',
            url: modifyAirlineDetailsURL, 
            headers: AuthService.getAuthHeader().headers,
            data: this.state.newAirline
        }).then(() => {          
            this.renewUserToken();
        }).catch((error) => {
            ErrorService.logErrorToConsole(error);
            this.setState({ 
                updateState: 'error',
            })
        })
    }

    // Renewing the user token with the updated details.
    renewUserToken = () => {
        let newAirUsername = this.state.newAirline.UserName;
        let newAirPassword = this.state.newAirline.Password;
        if (this.context.updateUserInfo([newAirUsername, newAirPassword]))
        {
            this.setState({ 
                airline: this.state.newAirline,
                updateState: 'updated',
                editMode: false
            }, this.setCountryName)
        }
        else
        {
            this.setState({ 
                airline: this.state.newAirline,
                updateState: 'tokenUpdateError',
                editMode: false
            })
        }
    }

    // When the airline changes its password using the PasswordChange component, it handles everything:
    handlePasswordChange = (newPassword) => {
        let airlineClone = Object.assign({}, this.state.newAirline);
        airlineClone.Password = newPassword;
        this.setState({
            newAirline: airlineClone
        })
        this.renewUserToken();
    }

    // Gets the country name using the given Id.
    getCountryName = (id) => {
        let country = this.state.countries.find(country => {
            return country.Id == id;
        })
        return country.Name;
    }

    componentDidMount() {
        this.getCountries();
    }

    componentWillUnmount() {
        this.source.cancel();
    }

    render() {

        const airline = this.state.airline;
        if (this.state.countries == 'loading')
        {
            return (
                <img src={loadingData} alt="loading..." className="loading-user-view"/>
            )
        }

        if (this.state.countries == 'error')
        {
            return (
                <div className="error-container">
                    <img src={errorIcon} alt="error" className="error-icon"/>
                    <h4 className="error-message">An error occurred while receiving the data. </h4>
                </div>
            )
        }
        return (
            <div id="airline-details-container">
                <h2 id="airline-details-heading">Company Details</h2>
                <div id="airline-details">

                    {/* When in edit mode, the details text blocks will be replaced with changable fields */}

                    {/* Left Column */}
                    <div id="air-details-left-column" className="details-column">

                        <div id="air-name" className="field-cont">
                            <label id="air-name" className="label">Name: &nbsp;</label>
                            {!this.state.editMode &&
                                <label id="air-name" className="text">{airline.Name}</label>
                            }
                            {this.state.editMode &&
                                <input type="text" id="Name" className="air-name" defaultValue={airline.Name} onChange={this.handleChange}/>
                            }
                        </div>

                        <div id="air-email" className="field-cont">
                            <label id="air-email" className="label">Email: &nbsp;</label>
                            {!this.state.editMode &&
                                <label id="air-email" className="text">{airline.Email}</label>
                            }
                            {this.state.editMode &&
                                <input type="text" id="Email" className="air-email" defaultValue={airline.Email} onChange={this.handleChange}/>
                            }
                        </div>

                        <div id="air-country" className="field-cont">
                            <label id="air-country" className="label">Country: &nbsp;</label>
                            {!this.state.editMode &&
                                <label id="air-country" className="text">{this.getCountryName(this.state.airline.CountryCode)}</label>
                            }
                            {this.state.editMode &&
                                <select id="CountryCode" className="country-select" onChange={this.handleChange} value={airline.CountryCode}>
                                    {this.state.countries.map(country => <option key={country.Id} value={country.Id}>{country.Name}</option>) }
                                </select>
                            }
                        </div>

                    </div>
                    
                    {/* Right Column */}
                    <div id="air-details-right-column" className="details-column">

                        <div id="air-user-name" className="field-cont">
                            <label id="air-user-name" className="label">User Name: &nbsp;</label>
                            {!this.state.editMode &&
                                <label id="air-user-name" className="text">{airline.UserName}</label>
                            }
                            {this.state.editMode &&
                                <input type="text" id="UserName" className="air-user-name" defaultValue={airline.UserName} onChange={this.handleChange}/>
                            }
                        </div>

                        <div id="air-password" className="field-cont">
                            <PasswordChange userType="airline" handlePasswordChange={this.handlePasswordChange}/>
                        </div>

                    </div>

                </div>

                {!this.state.editMode &&
                    <Button id="edit-air-details" text="Edit" icon={editIcon} onClick={()=>this.setState({editMode: true})}/>
                }
                {this.state.editMode &&
                    <div className="air-edit-buttons">
                        <Button id="cancel-air-details" text="Cancel" icon={cancelIcon} onClick={this.cancelEditMode}/>
                        <Button id="save-air-details" text="Save" icon={saveIcon} onClick={this.updateAirlineDetails}/>
                        {this.state.updateState == 'loading' &&
                            <img src={loadingIcon} alt="" className="loading-update-user"/>
                        }
                    </div>
                }

                <SweetAlert 
                    warning
                    show={this.state.validationAlert}
                    showConfirm
                    allowEscape
                    title="All fields must be filled."
                    onConfirm={() => this.setState({validationAlert: false})}
                    >
                </SweetAlert>

                <SweetAlert 
                    error
                    show={this.state.updateState == 'error'}
                    showConfirm
                    allowEscape
                    title="An error occurred while updating the data."
                    onConfirm={() => this.setState({updateState: 'none'})}
                    >
                </SweetAlert>

                <SweetAlert 
                    error
                    show={this.state.updateState == 'tokenUpdateError'}
                    showConfirm
                    allowEscape
                    title="An error occurred while updating your login token"
                    onConfirm={() => this.setState({updateState: 'none'})}
                    >
                    We recommended you to logout and then login again.
                </SweetAlert>

            </div>
        )
    }
}

AirlineDetails.contextType = UserContext;

export default AirlineDetails;