import React, {Component} from 'react';
import axios from 'axios';
import SweetAlert from 'react-bootstrap-sweetalert';
import AuthService from '../../services/AuthService';
import ErrorService from '../../services/ErrorService';
import CountriesService from '../../services/CountriesService';
import {UserContext} from '../../UserContext';
import FlightContainer from './FlightContainer';
import { getAirlineFlightsURL, cancelFlightURL } from '../../Constants';
import loadingData from '../../assets/loading-data.gif';
import errorIcon from '../../assets/error-icon.png'
import infoIcon from '../../assets/info-icon.png';
import '../../styles/Airline.css';

// This component is a part of the airline company area.
// It displays all the airline's flights and allows it to update or cancel them.
class ManageFlights extends Component {

    state = {
        flights: [],
        flightsState: 'lodaing',
        batchesToDisplay: 1,
        isFiltering: false,
        'origin-country-filter': '',
        'dest-country-filter': '',
    }

    CancelToken = axios.CancelToken;
    source = this.CancelToken.source();


    // Gets all the flights of the airline from the server.
    getAirlineFlights = async () => {

        await axios({
            url: getAirlineFlightsURL, 
            headers: AuthService.getAuthHeader().headers,
            cancelToken: this.source.token
        }).then((response) => {
            this.setState({ 
                flights: response.data,
                flightsState: 'loaded'
            })
        }).catch((error) => {
            ErrorService.logErrorToConsole(error);
            this.setState({ 
                flightsState: 'error'
            })
        })
    }

    // Deletes a flight from the server.
    deleteFlight = (flightId, handleError) => {
        axios({
            method: 'DELETE',
            url: cancelFlightURL + '/' + flightId, 
            headers: AuthService.getAuthHeader().headers,
        }).then(() => {
            this.removeFlightFromList(flightId);
        }).catch((error) => {
            ErrorService.logErrorToConsole(error);
            handleError();
        })
    }

    // Removes the flight from the view.
    removeFlightFromList = (flightId) => {
        let newFlightsList = [];
        this.state.flights.forEach(flight => {
            if (flight.Id != flightId)
                newFlightsList.push(flight);
        });
        this.setState({flights: newFlightsList}, 
            this.mapFlights)
    }

    // Handle the changes of the fields in the update form.
    handleChange = (e) => {
        this.setState({
            [e.target.id]: e.target.value
        }, this.mapFlights)
    }

    // When the user scrolls down to the bottom - it adds more flights to the list.
    handleScroll = (e) => {
        const bottomReached = e.target.scrollHeight - e.target.clientHeight == e.target.scrollTop
        if (bottomReached)
        {
            this.setState({batchesToDisplay: this.state.batchesToDisplay + 1})
        }
    }

    componentDidMount() {
        this.getAirlineFlights();
    }

    componentWillUnmount() {
        this.source.cancel();
    }

    render() {
        if (this.state.flightsState == 'loading')
        {
            return (
                <img src={loadingData} alt="" className="loading-user-view"/>
            )
        }
        if (this.state.flightsState == 'error')
        {
            return (
                <div className="error-container">
                    <img src={errorIcon} alt="error" className="error-icon"/>
                    <h4 className="error-message">An error occurred while receiving the data.</h4>
                </div>
            )
        }

        const info = 'Flights that landed 3 days ago or more won\'t be showed here.'

        const displayedFlights = this.state.flights.filter((flight, index)=>{
            return (index < this.state.batchesToDisplay*50)
        }).map(flight => {
            return <FlightContainer flight={flight} key={flight.Id} deleteFunc={this.deleteFlight}/>
        })

        let totalFlights;

        if (displayedFlights.length == this.state.flights.length)
        {
            totalFlights = `Displaying ${this.state.flights.length} flights:`;
        }

        if (displayedFlights.length < this.state.flights.length)
        {
            totalFlights = `Displaying ${displayedFlights.length} out of ${this.state.flights.length} flights:`;
        }

        return (
            <div id="manage-flights-container">

                <p className="instructions" id="manage-flights-instructions">
                    Here you can see and manage all your flights.&nbsp;
                    <img src={infoIcon} title={info} alt="" className="info-icon"/>
                </p>

                { this.state.flightsState == "loaded" && 
                    <label id="total-flights">{totalFlights}</label>
                }

                {this.state.flights.length == 0 &&
                    <label id="no-airline-flights">No flights to show.</label>
                }
                
                <div id="airline-flights-list" className="list-view" onScroll={this.handleScroll}>
                    {displayedFlights}
                    {displayedFlights.length != this.state.flights.length &&
                        <img src={loadingData} alt="" id="filter-loading"/>
                    }
                </div>

            </div>
        )
    }
}

ManageFlights.contextType = UserContext;

export default ManageFlights;