import React from 'react';
import { flightsBoardURL } from '../Constants';

// This component contains the flights board page.
// NOTICE: The flights board is an external page. It is being presented here inside an iframe element.
const FlightsBoard = function(props) {

    return (
        <div id="flights-board-container" className="page-content-container">
            <iframe id="board-frame" src={flightsBoardURL} className="iframe-cont" title="flights-board"></iframe>
        </div>
    )
}

export default FlightsBoard;