import React, {Component} from 'react';
import axios from 'axios';
import SweetAlert from 'react-bootstrap-sweetalert';
import AuthService from '../../services/AuthService';
import ErrorService from '../../services/ErrorService';
import CountriesService from '../../services/CountriesService';
import Button from '../../components/Button';
import HiddenField from '../HiddenField';
import { updateAirlineURL } from '../../Constants';
import airlineIcon from '../../assets/airline-icon.png';
import arrowsIcon from '../../assets/arrows-icon.png';
import editIcon from '../../assets/edit-icon.png';
import deleteIcon from '../../assets/delete-icon.png';
import saveIcon from '../../assets/save-icon.png';
import cancelIcon from '../../assets/cancel-icon.png';
import loadingIcon from '../../assets/loading-data.gif';

// This is a child component of the ManageAirlines component.
// It presents all the information about the airline company and allows the user to
// update it or delete the airline.
class AirlineContainer extends Component {

    state = {
        airline: this.props.airline,
        newAirline: this.props.airline,
        countries: [],
        moreDetails: false,
        editMode: false,
        updateState: 'none',
        validationMessage: '',
        deleteState: 'none',
        errorState: false
    }

    // Gets all the countries from the server, so they can be used in the dropdowns.
    getCountries = async () => {

        let countries = await CountriesService.getCountries();
        if (countries != null)
        {
            this.setState({
                countries: countries,
            });
        }
        else
        {
            this.setState({ errorState: true})
        }
    }

    // When the moreDetails state is true, the component will present more information of the airline:
    // username, password, and origin country.
    toggleMoreDetails = () => {
        this.setState({moreDetails: !this.state.moreDetails});
    }

    // When the user types any changes to the fields in edit mode, it will handle it.
    // All the changes will be reflected on the 'newAirline' state, while the regular 'airline' state
    // Will stay the same.
    handleChange = (e) => {
        let airlineClone = Object.assign({}, this.state.newAirline);
        airlineClone[e.target.id] = e.target.value;
        this.setState({
            newAirline: airlineClone
        })

        // Removes the validation error message, if there is one.
        this.setState({ 
            validationMessage: '',
        })
    }

    // This aborts the editing mode.
    cancelEdit = () => {
        this.setState({
            editMode: false,
            newAirline: this.state.airline
        });
    }

    // Updates the airline with the new information when the user clicks the 'save' button.
    updateAirline = () => {

        if (!this.validateAirlineDetails(this.state.newAirline))
        {
            this.setState({ 
                validationMessage: 'All fields must be filled.',
            })
            return;
        }

        this.setState({ 
            updateState: 'loading',
        })
        axios({
            method: 'PUT',
            url: updateAirlineURL, 
            headers: AuthService.getAuthHeader().headers,
            data: this.state.newAirline
        }).then(() => {
            this.setState({
                airline: this.state.newAirline,
                updateState: 'updated',
                editMode: false,
                moreDetails: true
            });
        }).catch((error) => {
            ErrorService.logErrorToConsole(error);
            this.setState({ 
                updateState: 'error',
            })
        })
    }

    // Validates that none of the customer's fields are empty before updating.
    validateAirlineDetails = (airline) => {
        if (airline.Name == '' ||
            airline.Email == '' ||
            airline.UserName == '' )
        {
            return false;
        }
        else
            return true;
    }

    // Present an alert for the user to confirm the delete operation.
    handleDeleteBtn = () => {
        this.setState({deleteState: 'deleteAlert'})
    }

    // When the user confirms to delete, this funcion will invoke the deletion function that
    // was recieved from the parent component, and then the parent will delete the airline.
    handleDeleteAirline = () => {
        this.setState({ 
            deleteState: 'deleting' 
        });
        this.props.deleteFunc(this.state.airline, this.deleteErrorHandle);
    }

    // When an error occurrs while deleting the airline.
    deleteErrorHandle = () => {
        this.setState({deleteState: 'error'})
    }

    // Gets the name of the country by its Id.
    getCountryName = (id) => {
        let country = this.state.countries.find(country => {
            return country.Id == id;
        })
        return country.Name;
    }

    componentDidMount() {
        this.getCountries();
    }

    render() {

        // Every airline's icon has a different color by using this style.
        const pictureStyle = { filter: `hue-rotate(${this.props.colorKey*50}deg)` }

        const airline = this.state.airline;

        if (this.state.deleteState == 'deleting')
        {
            return (
                <div className="airline-cont">
                    <img src={loadingIcon} alt="" className="loading-delete-element"/>
                </div>
            )
        }
        else if (!this.state.editMode)
        {
            return (
                <div className="airline-cont">
                    {/* This will render when the component mounts on the first time.
                        The basic airline details and the action buttons */}
                    <div className="airline-first-row">
                        <img src={airlineIcon} alt="" className="airline-icon" style={pictureStyle}/>
                        <label className="airline-id">{airline.Id}</label>
                        <label className="airline-name">{airline.Name}</label>
                        <label className="airline-email">{airline.Email}</label>

                        <Button id="more-details" onClick={this.toggleMoreDetails} text="More Details" icon={arrowsIcon}/>

                        <Button id="edit" onClick={()=>this.setState({editMode: true})} text="Edit" icon={editIcon}/>

                        <Button id="delete" onClick={this.handleDeleteBtn} text="Delete" icon={deleteIcon}/>
                    </div>
                    {/* When the user clicks "More Details" this will apear: */}
                    { this.state.moreDetails &&

                        <div className="airline-second-row">
                            <div className="airline-username-cont">
                                <label className="airline-username-label">Username:&nbsp;</label>
                                <label className="airline-username">{airline.UserName}</label>
                            </div>

                            <HiddenField id="airline-password" label="Password:" value={airline.Password}/>

                            <div className="airline-country-cont">
                                <label className="airline-country-label">Country:&nbsp;</label>
                                <label className="airline-country">{this.getCountryName(this.state.airline.CountryCode)}</label>
                            </div>
                        </div>
                    }
                    {/* An alert before removing the airline: */}
                    <SweetAlert 
                        show={this.state.deleteState == 'deleteAlert'}
                        showCancel
                        showConfirm
                        allowEscape
                        title="Confirm operation"
                        onConfirm={this.handleDeleteAirline}
                        onCancel={()=>{this.setState({deleteState: 'none'})}}
                        >
                        {`Are you sure you want to delete '${this.state.airline.Name}'?`}
                    </SweetAlert>

                    {this.state.deleteState == 'error' &&
                        <label className="airline-action-error">An error occurred while removing the airline.</label>
                    }
                </div>
            )
        }
        else // Edit mode:
        {
            if (this.state.errorState == true)
            {
                return (
                    <div className="airline-cont">
                        <label className="airline-error">An error occurred while editing the airline.</label>
                    </div>
                )
            }

            return (
                <div className="airline-cont">
                    <div className="airline-first-row">

                        <img src={airlineIcon} alt="" className="airline-icon" style={pictureStyle}/>
                        <label className="airline-id">{airline.Id}</label>
                        <input type="text" className="airline-name" id="Name" onChange={this.handleChange} defaultValue={airline.Name}/>
                        <input type="text" className="airline-email" id="Email" onChange={this.handleChange} defaultValue={airline.Email}/>

                    </div>

                    <div className="airline-second-row">
                        <div className="airline-username-cont">
                            <label className="airline-username-label">Username:&nbsp;</label>
                            <input type="text" className="airline-username" id="UserName" onChange={this.handleChange} defaultValue={airline.UserName}/>
                        </div>

                        <div className="airline-country-cont">
                            <label className="airline-country-label">Country:&nbsp;</label>
                            <select id="CountryCode" className="country-select" onChange={this.handleChange} defaultValue={this.state.airline.CountryCode}>
                                {this.state.countries.map(country => <option key={country.Id} value={country.Id}>{country.Name}</option>) }
                            </select>
                        </div>
                    </div>

                    <div className="airline-third-row">

                        <Button id="cancel" onClick={this.cancelEdit} text="Cancel" icon={cancelIcon}/>

                        <Button id="save" onClick={this.updateAirline} text="Save" icon={saveIcon}/>

                        <label className="validation-message">{this.state.validationMessage}</label>

                        {this.state.updateState == 'loading' &&
                            <img src={loadingIcon} alt="" className="loading-update-user"/>
                        }
                        {this.state.updateState == 'error' &&
                            <label id="airline-update-error">An error occurred while updating the data.</label>
                        }
                    </div>
                </div>
            )
        }
    }
}

export default AirlineContainer;