import React, {Component} from 'react';
import {Switch, Route, NavLink, Redirect} from 'react-router-dom';
import ViewService from '../services/ViewService';
import AdminInbox from './Admin/AdminInbox';
import ManageCustomers from './Admin/ManageCustomers';
import ManageAirlines from './Admin/ManageAirlines';
import '../styles/Admin.css';

// This is the admin area page.
// It contains the inbox, the manage-customers and the manage-airlines components.
class Admin extends Component {

    state = {
        mobileNavSelect: 'inbox'
    }

    handleChange = (e) => {
        this.setState({
            mobileNavSelect: e.target.value
        })
    }

    render() {
        const navActiveStyle = {
            fontWeight: 500,
            backgroundColor: 'rgb(255, 255, 255, 0.4)'
        }
        return (
            <div className="user-container">
                <div className="user-heading">Administrator Area</div>

                {!ViewService.isMobileView() && // Desktop view
                    <div>
                        {/* A side navigation bar: */}
                        <div className="nav-panel">
                            <NavLink className="nav-panel-option" activeStyle={navActiveStyle} to="/user-page/inbox">Inbox</NavLink>
                            <NavLink className="nav-panel-option" activeStyle={navActiveStyle} to="/user-page/customer-accounts">Customer Accounts</NavLink>
                            <NavLink className="nav-panel-option" activeStyle={navActiveStyle} to="/user-page/airline-accounts">Airline Accounts</NavLink>
                        </div>
                        {/* The child components will be rendered here: */}
                        <div className="user-view">
                            <Switch>
                                <Route path='/user-page/inbox' component={AdminInbox}/>
                                <Route path='/user-page/customer-accounts' component={ManageCustomers}/>
                                <Route path='/user-page/airline-accounts' component={ManageAirlines}/>
                            </Switch>
                            {/* Default page: */}
                            { this.props.path == '/user-page' && 
                                <Redirect to='/user-page/inbox'/>
                            }
                        </div>
                    </div>
                }

                {ViewService.isMobileView() && // Mobile view
                    <div>
                        {/* A side navigation bar: */}
                        <select id="admin-nav-select" className="nav-select form-control" onChange={this.handleChange} value={this.state.mobileNavSelect}>
                            <option key="0" value="inbox">Inbox</option>
                            <option key="1" value="customer-accounts">Customer Accounts</option>
                            <option key="2" value="airline-accounts">Airline Accounts</option>
                        </select>

                        {this.state.mobileNavSelect == 'inbox' &&
                            <Redirect to='/user-page/inbox'/>
                        }

                        {this.state.mobileNavSelect == 'customer-accounts' &&
                            <Redirect to='/user-page/customer-accounts'/>
                        }

                        {this.state.mobileNavSelect == 'airline-accounts' &&
                            <Redirect to='/user-page/airline-accounts'/>
                        }

                        <div className="user-view-mobile">
                            <Switch>
                                <Route path='/user-page/inbox' component={AdminInbox}/>
                                <Route path='/user-page/customer-accounts' component={ManageCustomers}/>
                                <Route path='/user-page/airline-accounts' component={ManageAirlines}/>
                            </Switch>
                            {/* Default page: */}
                            { this.props.path == '/user-page' && 
                                <Redirect to='/user-page/inbox'/>
                            }
                        </div>
                    </div>
                }


            </div>
        )
    }
}

export default Admin;