import React, {Component} from 'react';

// This component renders the date select field for the flights search form.
class DateSelect extends Component {

    // Props: id, onChange

    handleChange = (e) => {
        this.props.onChange(e);
    }

    render() {
        if (this.props.show)
        {
            return (
                <div className="date-select-container">
                    <label htmlFor={this.props.id}>{this.props.labelText}</label>
                    <input type="date" id={this.props.id} name={this.props.id} className="form-control" value={this.props.value}
                        onChange={this.handleChange} required></input>
                </div>
            )
        }
        else
        {
            return null;
        }
    }
}

export default DateSelect;